import DataCollectorV2 from "../DataCollectorV2";
import CDUtils from "../../../technicalServices/CDUtils";

const StorageEventType = {
    'storage': 0,
};
export default class StorageEventCollectorV2 extends DataCollectorV2 {

    static configKey = 'isStorageEvents';

    constructor(utils, dataQ, elements, msgBus) {
        super();
        this._utils = utils;
        this._dataQ = dataQ;
        this._elements = elements;
        this._msgBus = msgBus;
    }

    getEvents() {
        return [
            'storage',
        ];
    }

    handleEvent = (event) => {
        let tabs = false;
        let keys = null;
        let action = null;
        if (event.key === null) {
            keys = null;
        }
        if (typeof event.key === 'string' && event.newValue === null) {
            keys = event.key;
            action = 'delete';
        } else {
            keys = event.key;
            action = 'update';
        }
        const tabList = this._utils.StorageUtils.getFromLocalStorage('cdTabList');
        if (tabList && typeof tabList[0] === 'object') {
            tabs = true;
        }
        tabs = tabs ? 1 : 0;
        this._addToCommunicationQueue(event.type, keys, action, tabs, event);
    }

    _addToCommunicationQueue(type, keys, action, tabsopened, e) {
        if (type === StorageEventType.storage) {
            this._msgBus.publish('storageEvent', { action: type });
        }

        const time = CDUtils.dateNow();

        const eventSeq = this._utils.StorageUtils.getAndUpdateEventSequenceNumber();

        this._dataQ.addToQueue('storage_events',
            this._utils.convertToArrayByMap(EventStructure, {
                eventSequence: eventSeq,
                timestamp: time,
                keys,
                action,
                tabsOpened: tabsopened,
            }));
    }
}