import DataCollector from "../DataCollector";
import Log from "../../technicalServices/log/Logger";
import AgenticBrowserDetectContract from "../../contract/staticContracts/AgenticBrowserDetectContract";
import {ConfigurationFields} from "../../core/configuration/ConfigurationFields";

const featureSettings = {
    configKey: 'isAgenticBrowserDetectFeature',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: true,
    shouldRun: false,
    isFrameRelated: false,
    runInUns: false,
    runInSlave: false,
    runInLean: false,
    isRunning: false,
    instance: null,
};

/**
 * AgenticBrowserDetectFeature
 *
 * Purpose:
 * - Collect static signals that may indicate the presence of an agentic browser.
 * - Currently focuses on detecting specific agentic-related fonts.
 * - Designed to be expanded in the future with additional static browser fingerprints.
 */
export default class AgenticBrowserDetectFeature extends DataCollector {
    static getDefaultSettings() {
        return featureSettings;
    }

    /**
     * @param {DataQueue} dataQ
     * @param {FontDetectorProvider} detectorProvider
     * @param {Object} configurationRepository - Repository for configuration values
     */
    constructor(dataQ, detectorProvider, configurationRepository) {
        super();
        this._dataQ = dataQ;
        this._detectorProvider = detectorProvider;
        this._configurationRepository = configurationRepository;
    }

    startFeature() {
        Log.info("Starting Agentic Browser Detection Feature");
        this._detectFonts().catch((error) => {
            Log.error(`Failed to collect Agentic Browser information: ${error}`);
        });
    }

    async _detectFonts() {
        const detectedFonts = await this.getDetectedFonts();

        if (!detectedFonts.length) {
            Log.info("No Agentic Browser fonts detected");
            return;
        }

        const agenticBrowserDetectContract = new AgenticBrowserDetectContract(detectedFonts.join(','));
        const contractData = agenticBrowserDetectContract.buildQueueMessage();
        this._dataQ.addToQueue('static_fields', contractData, false);
    }

    async getDetectedFonts() {
        let agenticBrowserFonts = this.getFontFromConfig();
        agenticBrowserFonts = this.fontsToArray(agenticBrowserFonts);
        return await this.arrayToFonts(agenticBrowserFonts);
    }

    /**
     * Detects which of the provided fonts are available in the browser.
     * @param {string[]} agenticBrowserFonts - Example: ["Arial", "Courier New", "AgenticFont"]
     * @returns {Promise<string[]>} - The subset of input fonts that were actually detected.
     */
    async arrayToFonts(agenticBrowserFonts) {
        const detector = this._detectorProvider.getBestDetector();
        const checks = await Promise.all(
            agenticBrowserFonts.map(async (font) => {
                try {
                    // The map produces an array of pairs, e.g., [["Arial", true], ["AgenticFont", false]]
                    return [font, await detector.detect(font)];
                } catch {
                    return [font, false];
                }
            })
        );

        return checks.filter(([, isDetected]) => isDetected).map(([font]) => font);
    }

    fontsToArray(agenticBrowserFonts) {
        // Ensure we always iterate an array
        if (!Array.isArray(agenticBrowserFonts)) {
            agenticBrowserFonts = (typeof agenticBrowserFonts === 'string' && agenticBrowserFonts)
                ? [agenticBrowserFonts]
                : [];
        }
        return agenticBrowserFonts;
    }

    getFontFromConfig() {
        let agenticBrowserFonts = this._configurationRepository.get(ConfigurationFields.agenticBrowserFonts);

        if (typeof agenticBrowserFonts === 'string') {
            try {
                agenticBrowserFonts = JSON.parse(agenticBrowserFonts);
            } catch {
                // If parsing fails, treat as "no fonts configured"
                agenticBrowserFonts = [];
            }
        }
        return agenticBrowserFonts;
    }
}
