import DataCollector from "../DataCollector";
import Log from "../../technicalServices/log/Logger";
import { MessageBusEventType } from "../../events/MessageBusEventType";
import { DomInjectionIndicatorType } from "../../technicalServices/agenticDomInjection/AgenticDomInjectionDetector";
import {ConfigurationFields} from "../../core/configuration/ConfigurationFields";

/**
 * AgenticDomInjectionCollector
 *
 * Purpose:
 * - Detect short-lived ("blink") DOM injections that match known agent overlay signatures.
 *
 * How it works:
 * - Listens to MessageBus mutation events:
 *   - MessageBusEventType.MutationAddedNodes
 *   - MessageBusEventType.MutationRemovedNodes
 * - When a matching element is ADDED, store its `performance.now()` timestamp in a WeakMap.
 * - When the same element is REMOVED, compute duration = now - addedAt.
 * - If duration <= `agenticDomInjectionBlinkThresholdMs`, emit a blink event.
 *
 * Output:
 * - Enqueues to `agentic_dom_injection_events` with `EventStructure`:
 *   [eventSequence, timestamp, eventType, indicatorType, blinkDurationMs]
 *
 * Configuration:
 * - `isAgenticDomInjectionFeature`: enable/disable this collector.
 * - `agenticDomInjectionBlinkThresholdMs`: max blink duration (ms) to count as injection.
 */
const featureSettings = {
    configKey: 'isAgenticDomInjectionFeature',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: false,
    shouldRun: false,
    isFrameRelated: false,
    runInUns: false,
    runInSlave: false,
    runInLean: false,
    isRunning: false,
    instance: null,
};

const DEFAULT_BLINK_THRESHOLD_MS = 100;

export const DomInjectionEventType = {
    blink: 0,
};

export const EventStructure = [
    'eventSequence',
    'timestamp',
    'eventType',
    'indicatorType',
    'blinkDurationMs',
];

export default class AgenticDomInjectionCollector extends DataCollector {
    static getDefaultSettings() {
        return featureSettings;
    }

    /**
     * @param {DataQueue} dataQueue
     * @param {MessageBus} messageBus
     * @param {CDUtils} utils
     * @param {ConfigurationRepository} configurationRepository
     * @param {AgenticDomInjectionDetector} detector
     */
    constructor(dataQueue, messageBus, utils, configurationRepository, detector) {
        super();
        this._dataQueue = dataQueue;
        this._messageBus = messageBus;
        this._utils = utils;
        this._configurationRepository = configurationRepository;
        this._blinkThresholdMs = this._getBlinkThresholdMs();
        this._detector = detector;

        // track elements that are added+removed quickly ("blink") using WeakMap.
        this._indicatorAddTimes = new WeakMap(); // Element -> { t: number, indicatorType: number }
    }

    startFeature = () => {
        try {
            this._messageBus.subscribe(MessageBusEventType.MutationAddedNodes, this._onNodesAdded);
            this._messageBus.subscribe(MessageBusEventType.MutationRemovedNodes, this._onNodesRemoved);
        } catch (err) {
            Log.error(`Failed starting AgenticDomInjectionCollector. msg: ${err.message}`, err);
        }
    };

    stopFeature = () => {
        try {
            this._messageBus.unsubscribe(MessageBusEventType.MutationAddedNodes, this._onNodesAdded);
            this._messageBus.unsubscribe(MessageBusEventType.MutationRemovedNodes, this._onNodesRemoved);
        } catch (err) {
            Log.error(`Failed stopping AgenticDomInjectionCollector. msg: ${err.message}`, err);
        }
    };

    _onNodesAdded = (event) => {
        const nowPerf = performance.now();
        const nodes = event?.addedNodes;
        if (!nodes || !nodes.length) return;

        for (let i = 0; i < nodes.length; i++) {
            const el = nodes[i];
            const indicatorType = this._detector.detect(el);
            if (indicatorType === DomInjectionIndicatorType.unknown) continue;

            this._indicatorAddTimes.set(el, { t: nowPerf, indicatorType });
        }
    };

    _onNodesRemoved = (event) => {
        const nowPerf = performance.now();
        const nodes = event?.removedNodes;
        if (!nodes || !nodes.length) return;

        for (let i = 0; i < nodes.length; i++) {
            const el = nodes[i];
            const rec = this._indicatorAddTimes.get(el);
            if (!rec) continue;

            const duration = nowPerf - rec.t;
            this._indicatorAddTimes.delete(el);
            if (duration >= 0 && duration <= this._blinkThresholdMs) {
                this._sendBlinkEvent(rec.indicatorType, duration);
            }
        }
    };

    _sendBlinkEvent(indicatorType, blinkDurationMs) {
        try {
            const eventSequence = this._utils.StorageUtils.getAndUpdateEventSequenceNumber();
            const timestamp = this.getEventTimestamp();
            
            this._dataQueue.addToQueue(
                'agentic_dom_injection_events',
                this._utils.convertToArrayByMap(EventStructure, {
                    eventSequence,
                    timestamp,
                    eventType: DomInjectionEventType.blink,
                    indicatorType,
                    blinkDurationMs: Math.round(blinkDurationMs),
                })
            );
        } catch (err) {
            Log.error(`Failed sending AgenticDomInjectionBlink event. msg: ${err.message}`, err);
        }
    }

    _getBlinkThresholdMs() {
        const blinkThresholdMs = this._configurationRepository?.get(ConfigurationFields.agenticDomInjectionBlinkThresholdMs);
        return (typeof blinkThresholdMs === 'number' && blinkThresholdMs > 0) ? blinkThresholdMs : DEFAULT_BLINK_THRESHOLD_MS;
    }

    updateFeatureConfig() {
        this._blinkThresholdMs = this._getBlinkThresholdMs();
        // keep detector allowlist in sync with config updates
        this._detector?.onConfigUpdate?.();
    }
}

