import IframesQueue from "./IframesQueue";
import Detector from "../Detector";
import {ConfigurationFields} from "../../../core/configuration/ConfigurationFields";

export default class IframeDetector extends Detector {
    // TODO: refactor this class: split it into IframeDetector and IframeCache
    // TODO: create Detector and Cache classes
    _iframesQueue = new IframesQueue();
    _allFrames = [];
    _iframeRefs = [];
    _processedFrames = new WeakSet();
    _processDone = false;
    _processingStarted = false;
    _pendingDocuments = 0;
    _resolveDone = null;
    _donePromise = null;
    _drainingQueue = false;

    constructor(configurationRepository) {
        super();
        this._enableFramesProcessing = configurationRepository.get(ConfigurationFields.enableFramesProcessing);
    }

    detect(document, onDetect) {
        if(!this._enableFramesProcessing) {
            return;
        }

        if (this._processDone) {
            this._handleProcessedFrames(onDetect);
            return Promise.resolve(this._allFrames);
        }

        if (!this._processingStarted) {
            this._processingStarted = true;
            this._donePromise = new Promise((resolve) => {
                this._resolveDone = resolve;
            });

            this._enqueueDocument(document, 0);
            this._scheduleQueueDrain(onDetect);
        }

        return this._donePromise;
    }

    addIframeToCache(iframeOrDocument, depth = 1) {
        const {doc, iframeElement} = this._resolveIframeInput(iframeOrDocument);
        if (!doc || this._processedFrames.has(doc)) return;

        this._processedFrames.add(doc);
        if (!this._allFrames.includes(doc)) {
            this._allFrames.push(doc);
        }

        if (iframeElement && !this._iframeRefs.find((ref) => ref.iframe === iframeElement)) {
            this._iframeRefs.push({iframe: iframeElement, depth});
        }
    }

    removeIframeFromCache(iframeOrDocument) {
        const {doc, iframeElement} = this._resolveIframeInput(iframeOrDocument);
        if (!doc) return;

        this._processedFrames.delete(doc);
        this._allFrames = this._allFrames.filter((frame) => frame !== doc);
        this._iframeRefs = this._iframeRefs.filter(({iframe}) => {
            if (iframeElement) {
                return iframe !== iframeElement;
            }
            try {
                const iframeDoc = iframe?.contentDocument || iframe?.contentWindow?.document;
                return iframeDoc !== doc;
            } catch (error) {
                return true;
            }
        });
    }

    _handleProcessedFrames(onDetect) {
        this._allFrames.forEach(frame => {
            if (this._processedFrames.has(frame)) {
                onDetect(frame);
            }
        })
    }

    _enqueueDocument(doc, depth) {
        if (!doc || this._processedFrames.has(doc)) return;
        this._pendingDocuments += 1;
        this._iframesQueue.enqueue({doc, depth});
    }

    _scheduleQueueDrain(onDetect) {
        if (this._drainingQueue) return;
        this._drainingQueue = true;

        const drainStep = () => {
            this._processNext(onDetect);

            if (!this._iframesQueue.isEmpty()) {
                setTimeout(drainStep, 0);
            } else {
                this._drainingQueue = false;
                this._tryFinalize();
            }
        };

        setTimeout(drainStep, 0);
    }

    _processNext(onDetect) {
        const next = this._iframesQueue.dequeue();
        if (!next) return;
        const {doc, depth} = next;

        try {
            this._processDocument(doc, onDetect, depth);
        } catch (error) {
            Log.warn(`BioCatch SDK: Could not attach to document at depth ${depth}:`, error.message);
        } finally {
            this._markDocumentDone();
        }
    }

    _processDocument(doc, onDetect, depth) {
        if (this._processedFrames.has(doc)) return;

        if (depth > 0) {
            this._processedFrames.add(doc);
            this._allFrames.push(doc);
            onDetect(doc);
        }

        if (!this._enableFramesProcessing) return;

        const iframes = doc.querySelectorAll('iframe');

        iframes.forEach((iframe) => {
            this._iframeRefs.push({iframe, depth});
            const processFrame = () => {
                try {
                    const iframeDoc = iframe.contentDocument || iframe.contentWindow?.document;
                    if (!iframeDoc) {
                        Log.warn(`BioCatch SDK: Cannot access iframe document at depth ${depth + 1} - likely cross-origin`);
                        return;
                    }

                    this._enqueueDocument(iframeDoc, depth + 1);
                    this._scheduleQueueDrain(onDetect);
                } catch (error) {
                    Log.warn(`BioCatch SDK: Could not access iframe at depth ${depth + 1}:`, error.message);
                }
            };

            // Process immediately if the iframe document is already available.
            processFrame();

            // Re-run when the iframe finishes loading in case the document was not ready.
            iframe.addEventListener('load', () => processFrame());
        });
    }

    _markDocumentDone() {
        this._pendingDocuments = Math.max(0, this._pendingDocuments - 1);
        this._tryFinalize();
    }

    _tryFinalize() {
        if (this._pendingDocuments === 0 && this._iframesQueue.isEmpty() && this._processingStarted && !this._processDone) {
            this._processDone = true;
            if (this._resolveDone) {
                this._resolveDone(this._allFrames);
                this._resolveDone = null;
            }
        }
    }

    _resolveIframeInput(iframeOrDocument) {
        if (!iframeOrDocument) {
            return {doc: null, iframeElement: null};
        }

        if (iframeOrDocument.tagName === 'IFRAME') {
            try {
                const doc = iframeOrDocument.contentDocument || iframeOrDocument.contentWindow?.document;
                return {doc, iframeElement: iframeOrDocument};
            } catch (error) {
                console.warn('BioCatch SDK: Cannot access iframe while updating cache:', error.message);
                return {doc: null, iframeElement: iframeOrDocument};
            }
        }

        if (iframeOrDocument.nodeType === Node.DOCUMENT_NODE) {
            return {doc: iframeOrDocument, iframeElement: null};
        }

        return {doc: null, iframeElement: null};
    }
}
