import Log from '../../../../technicalServices/log/Logger';

class CPUTimingService {
  constructor({ performance, log, worker, blob, url } = {}) {
    this._performance = performance || window.performance || Date;
    if (!this._performance.now) {
      this._performance.now = this._performance.webkitNow ? this._performance.webkitNow : () => +new Date;
    }
    this._log = log || Log;
    this._Worker = worker || window.Worker;
    this._Blob = blob || window.Blob;
    this._URL = url || window.URL;
  }

  /**
   * Runs comprehensive CPU performance benchmarks
   * @returns {Promise<Object>} Simplified performance metrics
   */
  async runBenchmarks() {
    try {
      const [basic, integer, float, memory, crypto] = await Promise.all([
        this.basicBenchmark(),
        this.integerBenchmark(),
        this.floatBenchmark(),
        this.memoryBenchmark(),
        this.cryptoBenchmark()
      ]);

      // Extract key metrics
      return {
        basicOpsPerMs: basic.opsPerMs,
        integerOpsPerMs: integer.opsPerMs,
        floatOpsPerMs: float.opsPerMs,
        memoryOpsPerMs: memory.opsPerMs,
        cryptoOpsPerMs: crypto.opsPerMs,
      };
    } catch (error) {
      this._log.debug('CPUTimingService - Error running benchmarks:', error);
      return this.getFallbackResult();
    }
  }

  /**
   * Basic CPU benchmark using Web Worker
   * @returns {Promise<Object>} Basic performance metrics
   */
  async basicBenchmark() {
    try {
      const workerCode = `
        self.onmessage = function(e) {
          try {
            const startTime = performance.now();
            let result = 0;
            for (let i = 0; i < 5000000; i++) {
              result += Math.sqrt(i);
            }
            const executionTime = performance.now() - startTime;
            
            self.postMessage({ 
              success: true, 
              executionTime: executionTime,
              operations: 5000000,
              opsPerMs: Number((5000000 / executionTime).toPrecision(16))
            });
          } catch (error) {
            self.postMessage({ success: false, error: error.message });
          }
        };
      `;

      return await this.runWorkerBenchmark(workerCode);
    } catch (error) {
      this._log.debug('CPUTimingService - Error in basic benchmark:', error);
      return { executionTime: 0, operations: 0, opsPerMs: 0 };
    }
  }

  /**
   * Integer arithmetic benchmark
   * @returns {Promise<Object>} Integer performance metrics
   */
  async integerBenchmark() {
    try {
      const workerCode = `
        self.onmessage = function(e) {
          try {
            const startTime = performance.now();
            let result = 0;
            for (let i = 0; i < 10000000; i++) {
              result = (result + i) * 31 | 0;
            }
            const executionTime = performance.now() - startTime;
            
            self.postMessage({ 
              success: true, 
              executionTime: executionTime,
              operations: 10000000,
              opsPerMs: Number((10000000 / executionTime).toPrecision(16))
            });
          } catch (error) {
            self.postMessage({ success: false, error: error.message });
          }
        };
      `;

      return await this.runWorkerBenchmark(workerCode);
    } catch (error) {
      this._log.debug('CPUTimingService - Error in integer benchmark:', error);
      return { executionTime: 0, operations: 0, opsPerMs: 0 };
    }
  }

  /**
   * Floating-point arithmetic benchmark
   * @returns {Promise<Object>} Float performance metrics
   */
  async floatBenchmark() {
    try {
      const workerCode = `
        self.onmessage = function(e) {
          try {
            const startTime = performance.now();
            let result = 0.1;
            for (let i = 0; i < 5000000; i++) {
              result += Math.sin(result) + Math.cos(i);
            }
            const executionTime = performance.now() - startTime;
            
            self.postMessage({ 
              success: true, 
              executionTime: executionTime,
              operations: 5000000,
              opsPerMs: Number((5000000 / executionTime).toPrecision(16))
            });
          } catch (error) {
            self.postMessage({ success: false, error: error.message });
          }
        };
      `;

      return await this.runWorkerBenchmark(workerCode);
    } catch (error) {
      this._log.debug('CPUTimingService - Error in float benchmark:', error);
      return { executionTime: 0, operations: 0, opsPerMs: 0 };
    }
  }

  /**
   * Memory access benchmark
   * @returns {Promise<Object>} Memory performance metrics
   */
  async memoryBenchmark() {
    try {
      const workerCode = `
        self.onmessage = function(e) {
          try {
            const startTime = performance.now();
            const arr = new Uint32Array(1000000);
            
            // Sequential write
            for (let i = 0; i < arr.length; i++) {
              arr[i] = i;
            }
            
            // Sequential read
            let sum = 0;
            for (let i = 0; i < arr.length; i++) {
              sum += arr[i];
            }
            
            const executionTime = performance.now() - startTime;
            
            self.postMessage({ 
              success: true, 
              executionTime: executionTime,
              operations: arr.length * 2, // write + read
              opsPerMs: Number(((arr.length * 2) / executionTime).toPrecision(16))
            });
          } catch (error) {
            self.postMessage({ success: false, error: error.message });
          }
        };
      `;

      return await this.runWorkerBenchmark(workerCode);
    } catch (error) {
      this._log.debug('CPUTimingService - Error in memory benchmark:', error);
      return { executionTime: 0, operations: 0, opsPerMs: 0 };
    }
  }

  /**
   * Cryptographic operations benchmark
   * @returns {Promise<Object>} Crypto performance metrics
   */
  async cryptoBenchmark() {
    try {
      const workerCode = `
        self.onmessage = function(e) {
          try {
            const startTime = performance.now();
            let result = 0;
            
            // Simple hash-like operations
            for (let i = 0; i < 1000000; i++) {
              let hash = i;
              hash = ((hash << 5) - hash) + i;
              hash = hash & hash; // Convert to 32-bit integer
              result += hash;
            }
            
            const executionTime = performance.now() - startTime;
            
            self.postMessage({ 
              success: true, 
              executionTime: executionTime,
              operations: 1000000,
              opsPerMs: Number((1000000 / executionTime).toPrecision(16))
            });
          } catch (error) {
            self.postMessage({ success: false, error: error.message });
          }
        };
      `;

      return await this.runWorkerBenchmark(workerCode);
    } catch (error) {
      this._log.debug('CPUTimingService - Error in crypto benchmark:', error);
      return { executionTime: 0, operations: 0, opsPerMs: 0 };
    }
  }

  /**
   * Runs a benchmark in a Web Worker
   * @param {string} workerCode - The worker code to execute
   * @returns {Promise<Object>} Benchmark results
   */
  async runWorkerBenchmark(workerCode) {
    return new Promise((resolve) => {
      try {
        const blob = new this._Blob([workerCode], { type: 'application/javascript' });
        const workerUrl = this._URL.createObjectURL(blob);
        const worker = new this._Worker(workerUrl);

        worker.onmessage = (e) => {
          worker.terminate();
          this._URL.revokeObjectURL(workerUrl);
          
          if (e.data.success) {
            resolve({
              executionTime: e.data.executionTime,
              opsPerMs: e.data.opsPerMs
            });
          } else {
            this._log.debug('CPUTimingService - Worker benchmark failed:', e.data.error);
            resolve({ executionTime: 0, operations: 0, opsPerMs: 0 });
          }
        };

        worker.onerror = (error) => {
          worker.terminate();
          this._URL.revokeObjectURL(workerUrl);
          this._log.debug('CPUTimingService - Worker error:', error);
          resolve({ executionTime: 0, operations: 0, opsPerMs: 0 });
        };

        worker.postMessage('start');
      } catch (error) {
        this._log.debug('CPUTimingService - Error creating worker:', error);
        resolve({ executionTime: 0, operations: 0, opsPerMs: 0 });
      }
    });
  }

  /**
   * Returns fallback result when benchmarks fail
   * @returns {Object} Fallback performance data
   */
  getFallbackResult() {
    return {
      basicOpsPerMs: 0,
      integerOpsPerMs: 0,
      floatOpsPerMs: 0,
      memoryOpsPerMs: 0,
      cryptoOpsPerMs: 0,
    };
  }
}

export default CPUTimingService; 