import Log from '../../../../technicalServices/log/Logger';
import { 
  getBrowserExtensionsUrls, 
  DETECTION_RESPONSE_HEADERS
} from './BrowserExtensionsUrlConstants';

/**
 * URL-based extension scanner that detects extensions by fetching their resources
 */
export default class BrowserExtensionsUrlScanner {
  constructor(window) {
    this._window = window;
    this.extensions = getBrowserExtensionsUrls();
  }

  /**
   * Extract extension ID from extension URL
   * Extracts the ID from any protocol://id/path format
   * @param {string} url - The extension URL
   * @returns {string|null} - The extension ID or null if invalid format
   */
  _extractExtensionId(url) {
    const match = url.match(/:\/\/([^\/]+)/);
    return match ? match[1] : null;
  }

  /**
   * Helper method to safely detect an extension by fetching its resource
   * @param {string} url - The extension URL to detect
   * @returns {Promise<{detected: boolean, id: string|null, timestamp: number|null}>} - Detection result with id and timestamp
   */
  async detectExtension(url) {
    const extensionId = this._extractExtensionId(url);
    if (!extensionId) {
      Log.debug(`[BrowserExtensionsUrlScanner] Invalid extension URL format: ${url}`);
      return { detected: false, id: null, timestamp: null };
    }

    try {
      const response = await fetch(url, { 
        method: 'HEAD',
      });
      
      // If fetch succeeds (status 200-299), extension is likely installed
      const detected = response.ok;
      
      // Extract last modified from response header and convert to timestamp
      const lastModified = response.headers.get(DETECTION_RESPONSE_HEADERS.LAST_MODIFIED);
      let timestamp = null;
      if (lastModified) {
        const date = new Date(lastModified);
        timestamp = isNaN(date.getTime()) ? null : date.getTime();
      }
      
      const result = { detected, id: extensionId, timestamp };
      
      return result;
    } catch (error) {
      // Fetch will fail if extension is not installed or not in cache (CORS/network error)
      Log.debug(`[BrowserExtensionsUrlScanner] Error detecting extension via URL ${url}: ${error.message}`);
      const result = { detected: false, id: extensionId, timestamp: null };
      return result;
    }
  }

  /**
   * Scan for all extensions with improved performance
   * @returns {Promise<Array>} - List of detected extensions with id and timestamp (or null)
   */
  async scan() {
    // Run detections in parallel with improved error handling
    const detectionPromises = this.extensions.map(async url => {
      try {
        const result = await this.detectExtension(url);
        return result;
      } catch (error) {
        Log.debug(`[BrowserExtensionsUrlScanner] Failed to detect extension via URL ${url}: ${error.message}`);
        return { detected: false, id: null, timestamp: null };
      }
    });

    const results = await Promise.all(detectionPromises);
    
    // Filter to only detected extensions and return with id and timestamp
    return results
      .filter(result => result.detected && result.id !== null)
      .map(result => ({ id: result.id, timestamp: result.timestamp }));
  }
}

