import StaticContract from "./StaticContract";
import Log from "../../technicalServices/log/Logger";


export default class BrowserExtensionsUrlContract extends StaticContract {
    /**
     * @param  {Array<{id: string, timestamp: number|null}>} extensions, Array of objects with id and timestamp
     */
    constructor(extensions) {
        super();
        this.validateParams(extensions);
        this.extensions = extensions;
    }

    validateParams(extensions) {
        let isValid = (
            Array.isArray(extensions) &&
            this._eachExtensionIsValid(extensions)
        )
        if (!isValid) {
            Log.warn(`wrong type in BrowserExtensionsUrlContract parameters. extensions : {expected: Array<{id: string, timestamp: number|null}>, received: ${extensions}}`)
        }
    }

    _eachExtensionIsValid(extensions) {
        for (let ext of extensions) {
            if (!(typeof ext === 'object' && ext !== null)) {
                return false;
            }
            if (typeof ext.id !== 'string') {
                return false;
            }
            if (ext.timestamp !== null && typeof ext.timestamp !== 'number') {
                return false;
            }
        }
        return true;
    }

    /**
     * @return  {[string, [string, Array]]} for example : ['browser_extensions_url', ["v1", [{id: 'ghbmnnjooekpmoecnnnilnnbdlolhkhi', timestamp: 1234567890}, ...]]]
     */
    buildQueueMessage() {
        let name = this.getName();
        let message = [name, ["v1", this.extensions]];
        this.validateMessage(message);
        return message;
    }

    validateMessage(message) {
        let isValid = (
            typeof message[0] === 'string' &&
            Array.isArray(message[1]) &&
            message[1].length === 2 &&
            typeof message[1][0] === 'string' &&
            Array.isArray(message[1][1])
        )
        if (!isValid) {
            Log.warn('BrowserExtensionsUrl - Contract verification failed')
        }
    }

    getName() {
        return 'browser_extensions_url';
    }
}

