export const dataToObject = (data, someNumberWtf) => {
    const d = {};
    d[data[0]] = data[1];
    const dataJson = JSON.parse(JSON.stringify(d));
    const dataScalars = _jsonToScalars(dataJson)
    // The Object Scheme the server is expecting for customer_metadata
    return {some_number_wtf: someNumberWtf, data: dataScalars}
}

export const collectorExperimental = (eventName) => {
    return eventName === 'key_events_v2' || eventName === 'mouse_events_v2' || eventName === 'elements_events_v2' || eventName === 'elements_v2'    }

const _jsonToScalars = (json) => {
    return Object.fromEntries(
        Object.entries(json).map(([k, v]) => [k, v == null ? null : JSON.stringify(v)])
    );
}