import Log from '../../../technicalServices/log/Logger';

const SAMPLES = 20;
const WORKLOAD = 0x400000;
const T_TABLE = {
    1: 63.66, 2: 9.925, 3: 5.841, 4: 4.604, 5: 4.032, 6: 3.707, 7: 3.499, 8: 3.355, 9: 3.25, 10: 3.169,
    11: 3.106, 12: 3.055, 13: 3.012, 14: 2.977, 15: 2.947, 16: 2.921, 17: 2.898, 18: 2.878, 19: 2.861,
    20: 2.845, 21: 2.831, 22: 2.819, 23: 2.807, 24: 2.797, 25: 2.787, 26: 2.779, 27: 2.771, 28: 2.763,
    29: 2.756, 30: 2.75, 32: 2.738, 34: 2.728, 36: 2.719, 38: 2.712, 40: 2.704, 42: 2.698, 44: 2.692,
    46: 2.687, 48: 2.682, 50: 2.678, 55: 2.668, 60: 2.66, 65: 2.654, 70: 2.648, 80: 2.639, 100: 2.626,
    150: 2.609, 200: 2.601
};

class CoreEstimatorService {
  constructor() {
    this.performance = window.performance || Date;
    if (!this.performance.now) {
      this.performance.now = this.performance.webkitNow ? this.performance.webkitNow : () => +new Date;
    }

    const workerCode = `"use strict";self.onmessage=function(e){for(var n=e.data;n;)n--;self.postMessage(null)};`;
    try {
        const blob = new Blob([workerCode], {type: 'application/javascript'});
        this.workloadUrl = URL.createObjectURL(blob);
    } catch(e) {
        Log.debug('CoreEstimatorService - Cannot create blob worker');
        this.workloadUrl = '';
    }
  }

  getCoreCount() {
    return new Promise((resolve) => {
      try {
        if (typeof Worker === 'undefined' || !this.workloadUrl) {
          return resolve(1);
        }

        const workers = [];
        let control;
        let controldata = [];

        this._iterate((workerSize, report) => {
          this._measure(workers, workerSize, SAMPLES, (data) => {
            if (workerSize === 1) {
              Array.prototype.push.apply(controldata, data);
              control = this._analyse(controldata);
              report(true);
            } else {
              const group = this._analyse(data);
              const gv_gs = group.uvariance / group.size;
              const cv_cs = control.uvariance / control.size;
              const tscore = (group.mean - control.mean) / Math.sqrt(gv_gs + cv_cs);
              const freedom = (Math.pow(gv_gs + cv_cs, 2)) /
                ((Math.pow(group.uvariance, 2) / (Math.pow(group.size, 2) * (group.size - 1))) +
                (Math.pow(control.uvariance, 2) / (Math.pow(control.size, 2) * (control.size - 1))));
              report(this._accept(tscore, freedom));
            }
          });
        }, (cores) => {
          workers.forEach(w => w.terminate());
          URL.revokeObjectURL(this.workloadUrl);
          resolve(cores);
        });
      } catch (error) {
        Log.debug('CoreEstimatorService - Error in getCoreCount:', error);
        resolve(1);
      }
    });
  }

  _measure(workers, workerSize, sampleSize, callback) {
    const samples = [];

    for (let i = workers.length; i < workerSize; i++) {
      workers.push(new Worker(this.workloadUrl));
    }

    this._loop((_repeat) => {
      let begin, left = workerSize;

      for (let i = 0; i < workerSize; i++) {
        workers[i].onmessage = () => {
          left--;
          if (!left) {
            sampleSize--;
            samples.push(this.performance.now() - begin);
            if (sampleSize) {
              _repeat();
            } else {
              callback(samples);
            }
          }
        }
      }

      for (let i = 0; i < workerSize; i++) {
        workers[i].postMessage(WORKLOAD);
      }
      begin = this.performance.now();
    });
  }

  _loop(body) {
    (function next() {
      body(next);
    }());
  }

  _iterate(test, answer) {
    let min = 1, max = Infinity;

    const repeat = (cores) => {
      test(1, () => {
        test(cores, (pass) => {
          if (pass) {
            min = cores;
            repeat(2 * cores);
          } else {
            max = cores;
            if (min === 1) {
              return answer(min);
            }
            search(min * 3 / 2, min / 4);
          }
        });
      });
    };
    
    const search = (center, pivot) => {
      test(1, () => {
        test(center, (pass) => {
          if (pass) {
            min = center;
            center += pivot;
          } else {
            max = center;
            center -= pivot;
          }
          if (max - min === 1) {
            return answer(min);
          }
          if (!pivot) {
            return answer(max - 1);
          }
          search(center, pivot >> 1);
        });
      });
    };

    repeat(2);
  }

  _analyse(data) {
    const len = data.length;
    if (!len) return null;

    let min = Infinity, max = -Infinity, sum = 0, sum_squared_datum = 0;
    for (let i = 0; i < len; i++) {
      const datum = data[i];
      if (datum < min) min = datum;
      if (datum > max) max = datum;
      sum += datum;
      sum_squared_datum += Math.pow(datum, 2);
    }

    const mean = sum / len;
    const mean_squared = Math.pow(mean, 2);
    let unbiased_variance = 0;

    if (len > 1) {
      unbiased_variance = (sum_squared_datum - len * mean_squared) / (len - 1);
    }

    return { size: len, mean: mean, uvariance: unbiased_variance };
  };

  _accept(tscore, freedom) {
    const keys = Object.keys(T_TABLE);
    const key_low = keys.reduce((p, c) => (freedom < c) ? p : c);
    const key_high = keys.reduce((p, c) => (freedom > c) ? p : c);
    const span = key_high - key_low;
    const critical = this._linear(T_TABLE[key_low], T_TABLE[key_high], (freedom - key_low) / span);
    return tscore < critical;
  }

  _linear(a, b, t) { return a + (b - a) * t; }
}

export default CoreEstimatorService; 