import Log from "../log/Logger";

/**
 * Serves as a bridge between the WasmLoader module available in the window object scope and the SDK
 */
export default class WasmModuleLoader {

    /**
     *
     * @param externalWASMModules
     */
    constructor(externalWASMModules = []) {
        this._wasmLoader = null;
        this._externalWASMModules = externalWASMModules;
        if (this._externalWASMModules.length > 0 && typeof window['WasmLoader'] === 'object') {
            this._wasmLoader = window['WasmLoader'];
        }
    }

    isWasmLoaderAvailable() {
        if (this._wasmLoader && typeof this._wasmLoader['loadKotlinModule'] === 'function') {
            return true;
        }
        Log.warn('WasmLoader is not available in the window scope or loadKotlinModule is not a function.');
        return false;
    }

    /**
     * Load an external Wasm module by its name.
     * @param name
     * @param callback
     * @returns {null}
     */
    loadExternalWASMModuleByName(name = '', callback = null) {

        if (!this._externalWASMModules || !Array.isArray(this._externalWASMModules)) {
            Log.warn('Attempted to get external Wasm module, but externalWASMModules is not an array.');
            return null;
        }
        const wasmModuleDesc = this._externalWASMModules.find(({ key }) => key === name);
        if (!wasmModuleDesc) {
            Log.warn(`No external Wasm module found with name "${name}".`);
            return null;
        }

        if (!this.isWasmLoaderAvailable()) {
            return null;
        }

        this._wasmLoader['loadKotlinModule'](wasmModuleDesc.url, wasmModuleDesc.key).then(() => {
            const wasmModule = this.getLoadedWasmModule(wasmModuleDesc.key);
            if (wasmModule) {
                Log.debug(`Successfully loaded external Wasm module "${name}".`);
                if (typeof callback === 'function') {
                    callback(wasmModule);
                }
            } else {
                Log.warn(`Failed to load external Wasm module "${name}".`);
                return null;
            }
        }).catch((error) => {
            Log.warn(`Failed to load external Wasm module "${name}" from URL "${wasmModuleDesc.url}": ${error.message}`);
            return null;
        });
    }

    /**
     * Obtain a loaded Wasm module by its name.
     * @param name
     * @returns {*}
     */
    getLoadedWasmModule(name = '') {
        if (!this._externalWASMModules.find(( { key }) => key === name)) {
            Log.warn(`Attempted to load a Wasm module with name "${name}", 
            but it is not available in externalWASMModules.`);
            return null;
        }
        if(this._wasmLoader) {
            const wasmModule = this._wasmLoader.getModule(name);
            if (wasmModule && typeof wasmModule === 'object' && typeof wasmModule['exports'] === 'object') {
                return wasmModule['exports'];
            }
        } else {
            Log.warn('Attempted to load a Wasm module, ' +
                'but no WasmLoader is available in the window scope or externalWASMModules is empty.');
            return undefined;
        }
    }
}