import { ConfigurationFields } from '../../core/configuration/ConfigurationFields';
import Log from '../../technicalServices/log/Logger';
export default class ContextCategoryService {

    constructor(configurationRepository) {
        this.onConfigUpdate(configurationRepository)
    }

    onConfigUpdate(configurationRepository) {
        this.enableAutoContextCategory = configurationRepository.get(ConfigurationFields.enableAutoContextCategory);

        const rawContextCategories = configurationRepository.get(ConfigurationFields.contextCategories);
        let contextCategories;

        try {
            contextCategories = typeof rawContextCategories === 'string' ? JSON.parse(rawContextCategories) : rawContextCategories;
        } catch (e) {
            Log.error('Failed to parse contextCategories JSON:', e);
            this.NORMALIZED_KEYWORDS = {};
            return;
        }

        if (typeof contextCategories !== 'object' || contextCategories === null || Array.isArray(contextCategories)) {
            Log.error('contextCategories must be an object:', contextCategories);
            this.NORMALIZED_KEYWORDS = {};
            return;
        }

        this.NORMALIZED_KEYWORDS = {};
        for (const [keyword, category] of Object.entries(contextCategories)) {
            if (typeof category !== 'string') {
                Log.warn(`Expected string for category of "${keyword}", got`, category);
                continue;
            }
            const normalizedKey = keyword.toLowerCase().trim();
            this.NORMALIZED_KEYWORDS[normalizedKey] = category;
        }
    }

    isVisible(el) {
        if (!el || el.nodeType !== Node.ELEMENT_NODE) return true;
        if (el.offsetParent === null && el.tagName !== 'BODY') return false;

        const style = window.getComputedStyle(el);
        return (
            style.display !== 'none' &&
            style.visibility !== 'hidden' &&
            el.getAttribute('aria-hidden') !== 'true'
        );
    }

    extractVisibleText(root = document.body, maxNodes = 5000, maxDepth = 10) {
        let textContent = '';
        let nodeCount = 0;
        const self = this;

        function traverseIterative(root) {
            const stack = [{ node: root, depth: 0 }];
            while (stack.length > 0) {
                const { node, depth } = stack.pop();
                if (nodeCount >= maxNodes || depth > maxDepth) continue;

                if (node.nodeType === Node.TEXT_NODE) {
                    const trimmed = node.textContent.trim();
                    if (trimmed.length > 2) {
                        textContent += trimmed + ' ';
                    }
                }

                if (node.nodeType === Node.ELEMENT_NODE && !self.isVisible(node)) continue;

                nodeCount++;
                node.childNodes.forEach(child => stack.push({ node: child, depth: depth + 1 }));

                if (node.shadowRoot) {
                    stack.push({ node: node.shadowRoot, depth: depth + 1 });
                }
            }
        }

        traverseIterative(root);
        return textContent.toLowerCase();
    }

    getMetaText() {
        const title = document.title || '';
        const metaDescription = document.querySelector('meta[name="description"]')?.content || '';
        const ogTitle = document.querySelector('meta[property="og:title"]')?.content || '';
        return `${title} ${metaDescription} ${ogTitle}`.toLowerCase();
    }

    matchCategory(text) {
        const normalizedText = text.toLowerCase();
        const scores = {};
        const matchedKeywords = {};

        for (const [keyword, category] of Object.entries(this.NORMALIZED_KEYWORDS)) {
            if (normalizedText.includes(keyword)) {
                scores[category] = (scores[category] || 0) + 1;
                (matchedKeywords[category] ||= []).push(keyword);

                if (scores[category] >= 3) break;
            }
        }
        const sortedScores = Object.entries(scores).sort((a, b) => b[1] - a[1]);
        const topCategory = sortedScores.length > 0 ? sortedScores[0][0] : '0';
        return topCategory;
    }

    _getLocation() {
        return window.location;
    }

    getCategory() {
        if (!this.enableAutoContextCategory) return '0';

        const urlText = this._getLocation().href.toLowerCase();
        let category = this.matchCategory(urlText);

        if (category === '0') {
            const metaText = this.getMetaText();
            category = this.matchCategory(metaText);
        }

        return category;
    }
};