import Log from '../../../technicalServices/log/Logger';

export default class TimingCache {
    /**
     * Constructor for TimingCache
     * @param {Object} storageUtils - Utility for handling storage operations.
     */
    constructor(storageUtils) {
        this._storageUtils = storageUtils;
        this._cacheKey = 'timingData';
        this._cacheExpiration = 7 * 24 * 60 * 60 * 1000; // 1 week in milliseconds
    }

    /**
     * Saves timing data to local storage
     * @param {Object} timingData - The timing data to save
     */
    async saveTimingData(timingData) {
        const dataToSave = {
            ...timingData,
            cachedAt: Date.now()
        };
        Log.debug(`[TIMING-COLLECTOR] Saving timing data to localStorage under key: ${this._cacheKey}`);
        await this._storageUtils.saveToLocalStorage(this._cacheKey, dataToSave);
    }

    /**
     * Retrieves stored timing data from local storage
     * @returns {Object|null} - The stored timing data or null if none exist or expired
     */
    getStoredTimingData() {
        const storedData = this._storageUtils.getFromLocalStorage(this._cacheKey);
        if (!storedData) {
            Log.debug(`[TIMING-COLLECTOR] No cached timing data found`);
            return null;
        }

        // Check if cache is expired
        const isExpired = (Date.now() - storedData.cachedAt) > this._cacheExpiration;
        if (isExpired) {
            Log.debug(`[TIMING-COLLECTOR] Cached timing data is expired`);
            return null;
        }

        // Log.debug(`[TIMING-COLLECTOR] Retrieved timing data from localStorage: ${JSON.stringify(storedData)}`);
        return storedData;
    }

    /**
     * Clears the cached timing data
     */
    clearCache() {
        this._storageUtils.removeFromLocalStorage(this._cacheKey);
        Log.debug(`[TIMING-COLLECTOR] Cleared timing data cache`);
    }
} 