class ArchitectureDetectorService {
  /**
   * @param {Object} logger - Logger instance for error logging
   * @param {Object} typedArrayFactory - Factory for creating typed arrays (for testing)
   */
  constructor(logger = null, typedArrayFactory = null) {
    this._logger = logger;
    this._typedArrayFactory = typedArrayFactory || {
      createFloat32Array: (length) => new Float32Array(length),
      createUint8Array: (buffer) => new Uint8Array(buffer),
      createUint32Array: (array) => new Uint32Array(array),
      createBigInt64Array: (length) => new BigInt64Array(length)
    };
  }

  /**
   * Detects CPU architecture using multiple methods for better reliability
   * 1. NaN sign bit behavior (IEEE 754)
   * 2. Pointer size detection
   * 3. Platform-specific indicators
   */
  _getNaNSignByte() {
    try {
      const f = this._typedArrayFactory.createFloat32Array(1);
      const u8 = this._typedArrayFactory.createUint8Array(f.buffer);
      f[0] = Infinity;
      f[0] = f[0] - f[0]; // NOSONAR: Intentional NaN creation for architecture detection
      
      // Check byte order and get the correct byte for sign bit
      // IEEE 754: sign bit is the most significant bit
      const isLittleEndian = this._typedArrayFactory.createUint8Array(
        this._typedArrayFactory.createUint32Array([0x12345678]).buffer
      )[0] === 0x78;
      return isLittleEndian ? u8[3] : u8[0];
    } catch (error) {
      if (this._logger) {
        this._logger.debug('ArchitectureDetectorService - Error getting NaN sign byte:', error);
      }
      return undefined;
    }
  }

  /**
   * Detects pointer size to help distinguish between 32-bit and 64-bit architectures
   */
  _getPointerSize() {
    try {
      // Check if we can create a 64-bit typed array (indicates 64-bit support)
      this._typedArrayFactory.createBigInt64Array(1);
      return 64;
    } catch (error) {
      return 32;
    }
  }

  /**
   * Detects endianness of the system
   */
  _getEndianness() {
    try {
      return this._typedArrayFactory.createUint8Array(
        this._typedArrayFactory.createUint32Array([0x12345678]).buffer
      )[0] === 0x78;
    } catch (error) {
      if (this._logger) {
        this._logger.debug('ArchitectureDetectorService - Error detecting endianness:', error);
      }
      return undefined;
    }
  }

  /**
   * Detects CPU architecture using multiple detection methods
   * @returns {Promise<Object>} Object containing test values
   */
  async detectArchitecture() {
    try {
      const signByte = this._getNaNSignByte();
      const pointerSize = this._getPointerSize();
      const isLittleEndian = this._getEndianness();
      
      return {
        signByte,
        pointerSize,
        isLittleEndian
      };
      
    } catch (error) {
      if (this._logger) {
        this._logger.debug('ArchitectureDetectorService - Error detecting architecture:', error);
      }
      return {
        signByte: undefined,
        pointerSize: undefined,
        isLittleEndian: undefined,
        error: error.message
      };
    }
  }
}

export default ArchitectureDetectorService; 