import Log from '../../../../technicalServices/log/Logger';

/**
 * Animation Timing Service for collecting FPS information
 */
class AnimationTimingService {
  constructor() {}

  /**
   * Gets the current FPS (frames per second)
   * @returns {Promise<number>} Current FPS value
   */
  async getFPS() {
    try {
      let lastFrameTime = performance.now();
      let frameCount = 0;
      let fps = 0;

      return new Promise((resolve) => {
        function updateFPS(now) {
          frameCount++;

          const delta = now - lastFrameTime;
          if (delta >= 1000) {
            fps = (frameCount / delta) * 1000;
            Log.debug(`AnimationTimingService - FPS calculated: ${fps.toFixed(4)}`);
            resolve(Math.round(fps * 10000) / 10000); // Return with 4 decimal places
            return; // Stop measuring
          }

          requestAnimationFrame(updateFPS);
        }

        requestAnimationFrame(updateFPS);
      });

    } catch (error) {
      Log.debug('AnimationTimingService - Error collecting FPS:', error);
      return 0;
    }
  }
}

export default AnimationTimingService; 