import DataCollectorV2 from "../DataCollectorV2";
import {IsTrustedValue} from "../../../collectors/events/IsTrustedValue";
import CDUtils from "../../../technicalServices/CDUtils";
import DependenciesSingleton from "../../injectors/DependenciesContainer";
import DOMUtils from "../../../technicalServices/DOMUtils";
import {defaultMaskedCoordinates} from "../../../core/masking/MaskingService";

export const MouseEventType = {
    mousemove: 0,
    mousedown: 1,
    mouseup: 2,
    click: 3,
    dblclick: 4,
    mouseleave: 5,
    mouseenter: 6,
    wheel: 7,
    mouseout: 8,
    mouseover: 9,
};

export default class MouseEventCollectorV2 extends DataCollectorV2 {

    static configKey = 'isMouseCollector';

    constructor(utils, elements, dataQ, maskingService) {
        super();
        this._utils = utils;
        this._elements = elements;
        this._dataQueue = dataQ;
        this._maskingService = maskingService;
    }

    getEvents() {
        return ['mousemove', 'mousedown', 'mouseup', 'click', 'dblclick'];
    }

    handleEvent = (event) => {
        // wheel delta is only in the case of mousewheel
        let button = event.which;
        if (event.type === 'mouseup') {
            // remembers the last button so that IE will know which click was clicked.
            this._lastButton = button;
        } else if (event.type === 'click' && button === 0) {
            // So IE will know which click was clicked.
            button = this._lastButton;
        }

        let eventType = MouseEventType[event.type];
        if (this._utils.isUndefinedNull(eventType)) {
            eventType = -1;
        }

        const wheelDelta = event.deltaY || event.deltaX || event.deltaZ || event.wheelDelta || 0;
        const relativeTs = this.getTimestampFromEvent(event);
        // we are not using CDUtils.convertToArrayByMap here for optimization reasons

        this._dataQueue.addToQueue('customer_metadata',
            [
                'mouse_events_v2',
                [
                    null,
                    this._utils.StorageUtils.getAndUpdateEventSequenceNumber(),
                    CDUtils.dateNow(),
                    eventType,
                    IsTrustedValue[event.isTrusted],
                    event.type === 'mousemove' ? 0 : this._elements.getElementHashFromEvent(event),
                    this._maskingService.shouldMaskCoordinates() ? defaultMaskedCoordinates : this._roundCoordinate(event.pageX),
                    this._maskingService.shouldMaskCoordinates() ? defaultMaskedCoordinates : this._roundCoordinate(event.pageY),
                    this._maskingService.shouldMaskCoordinates() ? defaultMaskedCoordinates : this._roundCoordinate(event.screenX),
                    this._maskingService.shouldMaskCoordinates() ? defaultMaskedCoordinates : this._roundCoordinate(event.screenY),
                    (button === null || button === undefined) ? -1 : button,
                    Math.round(wheelDelta),
                    this._maskingService.shouldMaskCoordinates() ? defaultMaskedCoordinates : this._roundCoordinate(event.clientX),
                    this._maskingService.shouldMaskCoordinates() ? defaultMaskedCoordinates : this._roundCoordinate(event.clientY),
                    event.detail || 0,
                    (event.button === null || event.button === undefined) ? -1 : event.button,
                    relativeTs,
                ]
            ]
        );
    }

    _roundCoordinate(coordinate) {
        if (coordinate) {
            return Math.round(coordinate);
        }

        return -1;
    }
}