import DataCollector from '../../DataCollector';
import IsPrivateBrowsingContract from "../../../contract/staticContracts/IsPrivateBrowsingContract";
import IsIncognitoContract from "../../../contract/staticContracts/IsIncognitoContract";
import Log from '../../../technicalServices/log/Logger';
import { ConfigurationFields } from '../../../core/configuration/ConfigurationFields';

const featureSettings = {
    configKey: 'isPrivateBrowsing',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: true,
    shouldRun: true,
    isFrameRelated: false,
    runInUns: true,
    runInSlave: false,
    runInLean: false,
    isRunning: false,
    instance: null,
};

export default class IsPrivateBrowsingFeature extends DataCollector {
    static getDefaultSettings() {
        return featureSettings;
    }

    constructor(dataQ, privateBrowsingDetector, incognitoDetector, devDebugDataQ, configurationRepository) {
        super();

        this._dataQ = dataQ;
        this._devDebugDataQ = devDebugDataQ;
        this._privateBrowsingDetector = privateBrowsingDetector;
        this._incognitoDetector = incognitoDetector;
        this._configurationRepository = configurationRepository;
    }

    async startFeature() {
        // Run detections in parallel - each handles its own detection, logging, and sending
        const promises = [this._detectOldPrivateBrowsing()];

        // Check if new incognito detection is enabled via configuration
        const enableIncognitoDetection = this._configurationRepository?.get(ConfigurationFields.enableIncognitoDetection) === true;
        // Only add new incognito detection if enabled via configuration
        if (enableIncognitoDetection) {
            promises.push(this._detectNewIncognito());
        }

        await Promise.allSettled(promises);
    }

    /**
     * Detects private browsing using the old detection method, logs result, and sends to queue
     * @returns {Promise<void>}
     */
    async _detectOldPrivateBrowsing() {
        try {
            // Get old private browsing detection result (boolean)
            const isPrivate = await new Promise((resolve) => {
                this._privateBrowsingDetector.detectPrivateMode((result) => {
                    resolve(result === true);
                });
            });

            Log.debug(`[IsPrivateBrowsing] Private browsing detection result: ${isPrivate}`);

            // Send old is_private_browsing data
            let isPrivateBrowsingContract = new IsPrivateBrowsingContract(isPrivate);
            let isPrivateData = isPrivateBrowsingContract.buildQueueMessage();
            this._dataQ.addToQueue('static_fields', isPrivateData, false);
        } catch (error) {
            Log.debug(`[IsPrivateBrowsing] Old private browsing detection failed: ${error}`);
        }
    }

    /**
     * Detects incognito mode using the new detection method, logs result, and sends to queue
     * @returns {Promise<void>}
     */
    async _detectNewIncognito() {
        try {
            // Get new incognito detection results (map)
            const incognitoResults = await this._incognitoDetector.detectIncognitoMode();

            Log.debug(`[IsPrivateBrowsing] Incognito detection results: ${JSON.stringify(incognitoResults)}`);

            // Send new is_incognito data to dev debug queue
            let isIncognitoContract = new IsIncognitoContract(incognitoResults);
            let isIncognitoData = isIncognitoContract.buildQueueMessage();
            this._devDebugDataQ.addToQueue('static_fields', isIncognitoData, false);
        } catch (error) {
            Log.debug(`[IsPrivateBrowsing] New incognito detection failed: ${error}`);
        }
    }
}
