import { ConfigurationFields } from '../../../../core/configuration/ConfigurationFields';
import Log from '../../../../technicalServices/log/Logger';
import FontMigrationStage from './types/FontMigrationStage';
import FontVersionType from './types/FontVersionType';

export const FONT_COLLECTION_DEFAULT_CONFIG = {
    migrationMode: FontMigrationStage.V1_ONLY,
    v1: {
        offloadFontsCollectionEnabled: false,
        offloadFontsRemoveDuplicated: true,
    },
    v2: {
        // undefined means use best available detector
        // we have to keep way to control detector type in any case of new browser limitations or some other limits
        detectorType: undefined, // can be undefined, 'canvas', 'native'

        fontsPerBatch: 100,
        delayBetweenBatchesMs: 0,

        adaptiveBatchEnabled: true,
        minFontsPerBatch: 100,
        maxFontsPerBatch: 1000,

        frameBudgetMs: 100,
        frameHistorySize: 3,

        ignoreFonts: [
            "STFangsong",
            "STHeiti",
            "STKaiti",
            "STXihei",
            "NanumGothic",
            "NanumMyeongjo",
            "Osaka",
            "Osaka-Mono",
            "YuppySC-Regular"
        ]
    },
};

/**
 * Configuration manager for font collection settings
 */
export default class FontConfiguration {

    constructor(configurationRepository) {
        this._configurationRepository = configurationRepository;
        this._logsTag = `[FONTS-COLLECTOR][CONFIG-MANAGER]`;
    }

    /**
     * Gets the V1 scanner configuration settings
     * @returns {Object} V1 scanner configuration
     */
    getV1ScannerConfig() {
        const offloadFontsCollectionEnabled = this._configurationRepository.get(ConfigurationFields.offloadFontsCollectionEnabled);
        const offloadFontsRemoveDuplicated = this._configurationRepository.get(ConfigurationFields.offloadFontsRemoveDuplicated);

        return {
            offloadFontsCollectionEnabled: offloadFontsCollectionEnabled ?? FONT_COLLECTION_DEFAULT_CONFIG.v1.offloadFontsCollectionEnabled,
            offloadFontsRemoveDuplicated: offloadFontsRemoveDuplicated ?? FONT_COLLECTION_DEFAULT_CONFIG.v1.offloadFontsRemoveDuplicated
        };
    }

    /**
     * Gets the V2 scanner configuration settings
     * @returns {Object} V2 scanner configuration with detectorType support
     * @property {number} fontsPerBatch - Number of fonts to process per batch
     * @property {number} delayBetweenBatchesMs - Delay between batches in milliseconds
     * @property {boolean} adaptiveBatchEnabled - Whether adaptive batch processing is enabled
     * @property {number} minFontsPerBatch - Minimum fonts per batch for adaptive processing
     * @property {number} maxFontsPerBatch - Maximum fonts per batch for adaptive processing
     * @property {number} frameBudgetMs - Time budget per frame in milliseconds
     * @property {number} frameHistorySize - Number of frames to track for adaptive processing
     * @property {string|undefined} detectorType - Specific detector type ('native' or 'canvas') or undefined for best available
     */
    getV2ScannerConfig() {
        const config = this._getParsedFontCollectionConfig();
        return {
            detectorType: config?.v2?.detectorType ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.detectorType,

            fontsPerBatch: config?.v2?.fontsPerBatch ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.fontsPerBatch,
            delayBetweenBatchesMs: config?.v2?.delayBetweenBatchesMs ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.delayBetweenBatchesMs,

            adaptiveBatchEnabled: config?.v2?.adaptiveBatchEnabled ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.adaptiveBatchEnabled,
            minFontsPerBatch: config?.v2?.minFontsPerBatch ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.minFontsPerBatch,
            maxFontsPerBatch: config?.v2?.maxFontsPerBatch ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.maxFontsPerBatch,

            frameBudgetMs: config?.v2?.frameBudgetMs ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.frameBudgetMs,
            frameHistorySize: config?.v2?.frameHistorySize ?? FONT_COLLECTION_DEFAULT_CONFIG.v2.frameHistorySize,
        };
    }

    /**
     * Gets the font collection configuration for migration and general settings
     * @returns {Object} Font collection configuration
     */
    getFontCollectionConfig() {
        return this._getParsedFontCollectionConfig();
    }

    /**
     * Gets the migration mode from configuration
     * @returns {string} Migration mode
     */
    getMigrationMode(defaultMode) {
        return this._getParsedFontCollectionConfig().migrationMode ?? defaultMode;
    }

    /**
     * Gets the detector type from V2 configuration
     * @returns {string|undefined} Detector type or undefined
     */
    getDetectorType() {
        return this._getParsedFontCollectionConfig()?.v2?.detectorType;
    }

    /**
     * Gets the list of fonts to ignore during font collection
     * @returns {Array<string>} Array of font names to ignore
     */
    getIgnoredFonts() {
        const config = this._getParsedFontCollectionConfig();
        const serverIgnoreFonts = config?.v2?.ignoreFonts ?? [];
        const defaultIgnoreFonts = FONT_COLLECTION_DEFAULT_CONFIG.v2.ignoreFonts;

        // Merge server and default ignore fonts, removing duplicates
        const mergedFonts = new Set([...serverIgnoreFonts, ...defaultIgnoreFonts]);
        return Array.from(mergedFonts);
    }


    getConfigByVersion(version) {
        if (version === FontVersionType.VERSION1) {
            return this.getV1ScannerConfig();
        } else if (version === FontVersionType.VERSION2) {
            return this.getV2ScannerConfig();
        }
    }

    /**
     * Parses and returns the font collection configuration
     * @private
     * @returns {Object} Parsed font collection configuration
     */
    _getParsedFontCollectionConfig() {
        try {
            return JSON.parse(this._configurationRepository.get(ConfigurationFields.fontCollection) || "{}");
        } catch (error) {
            Log.error(`${this._logsTag} Failed to parse font collection config: ${error.message}`);
            return {};
        }
    }
}
