import Log from "../../technicalServices/log/Logger";
import StaticContract from "./StaticContract";

export default class ClientHintsContract extends StaticContract {
    /**
     * @param  {Object} clientHintsData - Object containing all client hints data (brands, mobile, platform, etc.)
     */
    constructor(clientHintsData) {
        super();
        this.fieldName = 'client_hints_user_agent';
        this.value = clientHintsData;
        this.validateParams(clientHintsData);
    }

    validateParams(data) {
        if (data === null || typeof data !== 'object' || Array.isArray(data)) {
            Log.warn(`ClientHintsContract: Invalid data object. Expected non-null object, received: ${typeof data}`);
            return;
        }

        // Validate each field in the object
        Object.entries(data).forEach(([fieldName, value]) => {
            this.validateField(fieldName, value);
        });
    }

    validateField(fieldName, value) {
        // Allow undefined values (optional fields)
        if (value === undefined) {
            return;
        }

        // Field-specific validation
        const fieldValidations = {
            'brands': (val) => Array.isArray(val) && val.every(item => 
                typeof item === 'object' && 
                item !== null && 
                typeof item.brand === 'string' && 
                typeof item.version === 'string'
            ),
            'is_mobile': (val) => typeof val === 'boolean',
            'platform': (val) => typeof val === 'string',
            'architecture': (val) => typeof val === 'string',
            'bitness': (val) => typeof val === 'string',
            'model': (val) => typeof val === 'string',
            'platform_version': (val) => typeof val === 'string',
            'user_agent_full_version': (val) => typeof val === 'string',
            'full_version_list': (val) => Array.isArray(val) && val.every(item => 
                typeof item === 'object' && 
                item !== null && 
                typeof item.brand === 'string' && 
                typeof item.version === 'string'
            ),
            'wow64': (val) => typeof val === 'boolean'
        };

        const validator = fieldValidations[fieldName];
        if (!validator) {
            Log.warn(`ClientHintsContract: unknown field name: ${fieldName}`);
            return;
        }

        const isValidValue = validator(value);
        if (!isValidValue) {
            const expectedType = this._getExpectedType(fieldName);
            Log.warn(`ClientHintsContract: wrong value type for ${fieldName}. {expected: ${expectedType}, received: ${typeof value}, value: ${JSON.stringify(value)}`);
        }
    }


    _getExpectedType(fieldName) {
        const typeMap = {
            'brands': 'Array',
            'is_mobile': 'boolean',
            'platform': 'string',
            'architecture': 'string',
            'bitness': 'string',
            'model': 'string',
            'platform_version': 'string',
            'user_agent_full_version': 'string',
            'full_version_list': 'Array',
            'wow64': 'boolean'
        };
        return typeMap[fieldName] || 'unknown';
    }

    /**
     * @return  {[string, Object]} for example: ['client_hints_user_agent', {brands: [...], is_mobile: false, ...}]
     */
    buildQueueMessage() {
        const name = this.getName();
        const message = [name, this.value];
        this.validateMessage(message);
        return message;
    }

    validateMessage(message) {
        const isValidKey = typeof message[0] === 'string' && message[0] === this.fieldName;
        const isValidValue = typeof message[1] === 'object' && message[1] !== null && !Array.isArray(message[1]);
        
        if (!isValidKey || !isValidValue) {
            Log.warn(`ClientHintsContract - Contract verification failed. Expected field name: ${this.fieldName}, received: ${message[0]}, value type: ${typeof message[1]}`);
            return;
        }
    }

    getName() {
        return this.fieldName;
    }
}

