import Log from '../../../technicalServices/log/Logger';


export function createHiddenIframe(document, id = 'cd__frame__tmp') {
  const ifr = document.createElement('iframe');
  ifr.id = id;
  ifr.style.position = 'fixed';
  ifr.style.left = '0';
  ifr.style.top = '0';
  ifr.style.width = '0';
  ifr.style.height = '0';
  ifr.style.border = '0';
  ifr.style.opacity = '0';
  ifr.style.pointerEvents = 'none';
  ifr.setAttribute('aria-hidden', 'true');
  ifr.setAttribute('tabindex', '-1');
  try { ifr.setAttribute('inert', ''); } catch (ex) { Log.warn('TypographyFrameUtils: failed to set inert attribute on iframe', ex); }
  ifr.srcdoc = '<!doctype html><meta charset="utf-8"><title>blank</title>';
  return ifr;
}

export function ensureBody(doc) {
  if (!doc.body) {
    const body = doc.createElement('body');
    doc.documentElement.appendChild(body);
  }
}

export async function waitFontsReady(doc) {
  try {
    if (doc.fonts && doc.fonts.ready && typeof doc.fonts.ready.then === 'function') {
      await doc.fonts.ready;
    }
  } catch (ex) { Log.warn('TypographyFrameUtils: waiting for document.fonts.ready failed', ex); }
}

export function nextFrame(win) {
  return new Promise((res) => (win.requestAnimationFrame || win.setTimeout)(res, 16));
}

export function boxFromRect(r, fontFamily = '') {
  const width  = r.width  || 0;
  const height = r.height || 0;
  const left   = r.left   || 0;
  const top    = r.top    || 0;
  const right  = ('right'  in r ? r.right  : (left + width));
  const bottom = ('bottom' in r ? r.bottom : (top  + height));
  const x = ('x' in r ? r.x : left);
  const y = ('y' in r ? r.y : top);
  return { x, y, left, top, right, bottom, width, height, font: fontFamily || '' };
}

export function quantizeBox(box, step = 0.125) {
  const shouldQuantize = Number.isFinite(step) && step > 0;
  const q = (v) => (typeof v === 'number' && shouldQuantize ? Math.round(v / step) * step : v);
  return {
    x: q(box.x), y: q(box.y), left: q(box.left), top: q(box.top),
    right: q(box.right), bottom: q(box.bottom), width: q(box.width),
    height: q(box.height), font: box.font || ''
  };
}

async function safeWaitFonts(doc) {
  try {
    if (doc.fonts?.ready?.then) {
      await doc.fonts.ready;
    }
  } catch (ex) {
     Log.warn('TypographyFrameUtils: waiting for hostDocument.fonts.ready failed', ex);
  }
}

async function attachAndLoadIframe(hostDocument, ifr) {
  const onLoad = new Promise((resolve, reject) => {
    const cleanup = () => { try { ifr.onload = null; ifr.onerror = null; } catch (ex) { Log.warn('TypographyFrameUtils: iframe cleanup failed', ex); } };
    ifr.onload = () => { cleanup(); resolve(); };
    ifr.onerror = (e) => { cleanup(); reject(e); };
  });
  (hostDocument.body || hostDocument.documentElement).appendChild(ifr);
  await onLoad;
}

function getFrameContexts(ifr) {
  return { w: ifr.contentWindow, d: ifr.contentDocument };
}

function initNode(doc, win, buildNode) {
  ensureBody(doc);
  const node = buildNode({ doc, win });
  doc.body.appendChild(node);
  return node;
}

function isBoxValid(box) {
  return Number.isFinite(box.width) && box.width > 0 &&
         Number.isFinite(box.height) && box.height > 0;
}

function withinTolerance(box, lastValid, absTolPx, relTol) {
  const dw = Math.abs(box.width - lastValid.width);
  const dh = Math.abs(box.height - lastValid.height);
  const tolW = Math.max(absTolPx, (box.width || 0) * relTol);
  const tolH = Math.max(absTolPx, (box.height || 0) * relTol);
  return dw <= tolW && dh <= tolH;
}

function measureBox(node, win) {
  const rect = node.getBoundingClientRect();
  const ff = win.getComputedStyle(node, null).getPropertyValue('font-family') || '';
  return boxFromRect(rect, ff);
}

async function stabilizeMeasurements(node, win, { maxFrames, minSamples, stableHitsTarget, absTolPx, relTol }) {
  let validSamples = 0, stableHits = 0, lastValid = null, last = null;

  for (let i = 0; i < maxFrames; i++) {
    await nextFrame(win);
    const box = measureBox(node, win);

    if (isBoxValid(box)) {
      validSamples++;
      if (lastValid && withinTolerance(box, lastValid, absTolPx, relTol)) {
        stableHits++;
      } else {
        stableHits = 0;
      }
      lastValid = box;
      if (validSamples >= minSamples && stableHits >= stableHitsTarget) return box;
    }
    last = box;
  }
  return lastValid || last;
}

function safeRemove(el) {
  try { 
    if (el){
       el.remove(); 
      }
    } catch (ex) { 
        Log.warn('TypographyFrameUtils: element.remove() failed', ex); 
    }
}

export async function measureInEphemeralIframe({
  hostWindow,
  hostDocument,
  frameId,
  buildNode,             
  maxFrames = 12,
  minSamples = 3,
  stableHitsTarget = 2,
  absTolPx = 0.25,
  relTol = 0.0005,
  quantStep = 0.125,
}) {
  await safeWaitFonts(hostDocument);

  const ifr = createHiddenIframe(hostDocument, frameId);
  let node, out;

  try {
    await attachAndLoadIframe(hostDocument, ifr);
    const { w, d } = getFrameContexts(ifr);

    node = initNode(d, w, buildNode);
    await waitFontsReady(d);

    out = await stabilizeMeasurements(node, w, { maxFrames, minSamples, stableHitsTarget, absTolPx, relTol });
    return quantizeBox(out, quantStep);
  } finally {
    safeRemove(node);
    safeRemove(ifr);
  }
}