import Log from '../../../../technicalServices/log/Logger';

/**
 * Memory Bandwidth Timing Service for measuring memory access patterns
 * High device variation - different between mobile RAM (LPDDR4/5) vs desktop RAM (DDR4/5)
 */
class MemoryTimingService {
  constructor({ performance, log } = {}) {
    this._performance = performance || window.performance;
    this._log = log || Log;
  }

  /**
   * Measures memory bandwidth and access patterns
   * @returns {Promise<Object>} Memory bandwidth timing data
   */
  async measureMemoryBandwidth() {
    try {
      const sequentialReadBandwidth = await this.measureSequentialReadBandwidth();
      const sequentialWriteBandwidth = await this.measureSequentialWriteBandwidth();
      const randomAccessRate = await this.measureRandomAccessRate();
      const cacheThroughput = await this.measureCacheThroughput();
      const memoryLatency = await this.measureMemoryLatency();

      return {
        sequentialReadBandwidth: Number(sequentialReadBandwidth.toPrecision(16)), // MB/s
        sequentialWriteBandwidth: Number(sequentialWriteBandwidth.toPrecision(16)), // MB/s
        randomAccessRate: Number(randomAccessRate.toPrecision(16)), // accesses per ms
        cacheThroughput: Number(cacheThroughput.toPrecision(16)), // bytes per ms
        memoryLatency: Number((memoryLatency * 1000000).toPrecision(16)) // nanoseconds
      };
    } catch (error) {
      this._log.debug('MemoryTimingService - Error measuring memory bandwidth:', error);
      return this.getFallbackResult();
    }
  }

  async measureSequentialReadBandwidth() {
    try {
      const size = 16 * 1024 * 1024; // 16MB
      const array = new Uint8Array(size);
      for (let i = 0; i < array.length; i++) array[i] = i & 0xFF;
      const startTime = this._performance.now();
      let sum = 0;
      for (let i = 0; i < array.length; i++) sum += array[i];
      const endTime = this._performance.now();
      const duration = endTime - startTime;
      return (size / (duration / 1000)) / (1024 * 1024); // MB/s
    } catch (error) {
      this._log.debug('MemoryTimingService - Error in sequential read:', error);
      return 0;
    }
  }

  async measureSequentialWriteBandwidth() {
    try {
      const size = 16 * 1024 * 1024; // 16MB
      const array = new Uint8Array(size);
      const startTime = this._performance.now();
      for (let i = 0; i < array.length; i++) array[i] = i & 0xFF;
      const endTime = this._performance.now();
      const duration = endTime - startTime;
      return (size / (duration / 1000)) / (1024 * 1024); // MB/s
    } catch (error) {
      this._log.debug('MemoryTimingService - Error in sequential write:', error);
      return 0;
    }
  }

  async measureRandomAccessRate() {
    try {
      const arraySize = 1024 * 1024;
      const array = new Uint8Array(arraySize);
      const indices = new Uint32Array(100000);
      for (let i = 0; i < array.length; i++) array[i] = i & 0xFF;
      for (let i = 0; i < indices.length; i++) indices[i] = Math.floor(Math.random() * arraySize);
      const startTime = this._performance.now();
      let sum = 0;
      for (let i = 0; i < indices.length; i++) sum += array[indices[i]];
      const endTime = this._performance.now();
      const duration = endTime - startTime;
      return indices.length / duration; // accesses per millisecond
    } catch (error) {
      this._log.debug('MemoryTimingService - Error in random access:', error);
      return 0;
    }
  }

  async measureCacheThroughput() {
    try {
      const size = 65536; // 64KB - fits in L1 cache
      const array = new Uint8Array(size);
      for (let i = 0; i < array.length; i++) array[i] = i & 0xFF;
      const startTime = this._performance.now();
      let sum = 0;
      for (let pass = 0; pass < 10; pass++) {
        for (let i = 0; i < array.length; i++) sum += array[i];
      }
      const endTime = this._performance.now();
      const duration = endTime - startTime;
      return (array.length * 10) / duration; // bytes per millisecond
    } catch (error) {
      this._log.debug('MemoryTimingService - Error in cache performance:', error);
      return 0;
    }
  }

  async measureMemoryLatency() {
    try {
      const arraySize = 1024 * 1024;
      const array = new Uint32Array(arraySize);
      for (let i = 0; i < array.length - 1; i++) array[i] = i + 1;
      array[array.length - 1] = 0;
      const startTime = this._performance.now();
      let current = 0;
      let steps = 0;
      for (let i = 0; i < 100000; i++) {
        current = array[current];
        steps++;
      }
      const endTime = this._performance.now();
      const duration = endTime - startTime;
      return duration / steps; // milliseconds per access
    } catch (error) {
      this._log.debug('MemoryTimingService - Error in memory latency:', error);
      return 0;
    }
  }

  getFallbackResult() {
    return {
      sequentialReadBandwidth: 0,
      sequentialWriteBandwidth: 0,
      randomAccessRate: 0,
      cacheThroughput: 0,
      memoryLatency: 0
    };
  }
}

export default MemoryTimingService; 