import Detector from "../Detector";

export default class ElementsDetector extends Detector {

    constructor() {
        super();
        this._elementsToDetect = [];
    }

    detect(document, onDetect) {
        const inputSelectors = "input:not([type='color']):not([type='hidden']):not([type='file'])" +
            ":not([type='image']):not([type='button']):not([type='radio']):not([type='checkbox']), textarea";
        document.querySelectorAll(inputSelectors).forEach((inputElement) => {
            onDetect(inputElement);
        })


        const clickSelectors = "input[type='button'], input[type='radio'], input[type='checkbox'], button";
        document.querySelectorAll(clickSelectors).forEach((inputElement) => {
            onDetect(inputElement);
        })

        const selectSelectors = 'select';
        document.querySelectorAll(selectSelectors).forEach((inputElement) => {
            onDetect(inputElement);
        })
    }

}