import Log from "../../technicalServices/log/Logger";
import DataCollector from '../DataCollector';
import { MessageBusEventType } from '../../events/MessageBusEventType';

const CATEGORY_FIRST_NAME = '0100';
const CATEGORY_LAST_NAME = '0101';
const CATEGORY_EMAIL = '0103';

const wasmModuleDefaultName = 'cdld';
const levenshteinScoreFunction = 'levenshtein';
const levenshteinMatchEmailFunction = 'matchNameEmail';

const featureSettings = {
    configKey: 'isLevenshteinDistanceCollector',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: false,
    shouldRun: false,
    isFrameRelated: false,
    runInSlave: false,
    runInLean: false,
    runInUns: false,
    isRunning: false,
    instance: null,
};

export const EventStructure = ['eventSequence', 'timestamp','similarity', 'score'];

/**
 * This collector relies on external WASM modules provided using startup configurations.
 * The collector is responsible for loading the WASM module and using its functions to calculate the Levenshtein distance
 * between a name and an email address.
 * It listens to events of type `MessageBusEventType.LevenshteinDistance` to receive updates on the name and email,
 * The elements from which the name and email are collected are detected by the categoryService
 */
export default class LevenshteinDistanceCollector extends DataCollector {
    static getDefaultSettings() {
        return featureSettings;
    }

    /**
     *
     * @param utils
     * @param configurationRepository
     * @param dataQ
     * @param MessageBus
     * @param moduleLoader {WasmModuleLoader}
     * @param categoryService {CategoryService} URL to the WASM module, if not provided it will use the default one
     */
    constructor(utils, configurationRepository, dataQ, MessageBus,
                moduleLoader, categoryService) {
        super();
        this._utils = utils;
        this._configurationRepository = configurationRepository;
        this._dataQ = dataQ;
        this._messageBus = MessageBus;
        this._moduleLoader = moduleLoader;
        this._categoryService = categoryService;

        this._levenshteinScoreFunction = null;
        this._levenshteinMatchEmailFunction = null;
        this._boundHandleLevenshteinDistanceEvent = this._handleLevenshteinDistanceEvent.bind(this);

    }

    startFeature() {
        super.startFeature();
        this._moduleLoader.loadExternalWASMModuleByName(
            wasmModuleDefaultName,
            this._loadWasmMethod.bind(this));
    }

    stopFeature() {
        super.stopFeature();
        this._messageBus.unsubscribe(MessageBusEventType.StandardInputBlurEvent, this._boundHandleLevenshteinDistanceEvent);
    }

    /**
     * Do not subscribe to events until callback is successfully called
     * @param wasmModule
     * @private
     */
    _loadWasmMethod(wasmModule) {
        if (wasmModule && wasmModule[levenshteinScoreFunction] &&
            typeof wasmModule[levenshteinScoreFunction] === 'function' &&
            typeof wasmModule[levenshteinMatchEmailFunction] === 'function') {
            this._levenshteinScoreFunction = wasmModule[levenshteinScoreFunction];
            this._levenshteinMatchEmailFunction = wasmModule[levenshteinMatchEmailFunction];
            this._messageBus.subscribe(MessageBusEventType.StandardInputBlurEvent, this._boundHandleLevenshteinDistanceEvent);
        } else {
            Log.error('Failed to load Levenshtein distance function from wasm module');
        }
    }


    /**
     * detect browsers by feature detection since user agent can lie
     */
    _handleLevenshteinDistanceEvent = (e) => {
        let newValue = e.target.value || '';

        if (newValue !== '') {
            // call LevenshteinDistanceFeature event
            const category = this._categoryService.categoryField(e.target);

            if (category === CATEGORY_FIRST_NAME || category === CATEGORY_LAST_NAME) {
                this.name = newValue;
            }

            if (category === CATEGORY_EMAIL) {
                this.email = newValue;
            }
            // Makes sure we have DistanceMeasure on the window,
            // also makes sure both name & email exists and not empty
            if (this.name && this.email) {
                const time = this.getEventTimestamp();
                const eventSeq = this._utils.StorageUtils.getAndUpdateEventSequenceNumber();

                const score = this._levenshteinScoreFunction(this.name, this.email);
                const match = this._levenshteinMatchEmailFunction(this.name, this.email);

                this._dataQ.addToQueue('similarity_score',
                    this._utils.convertToArrayByMap(EventStructure,
                        {
                            eventSequence: eventSeq,
                            timestamp: time,
                            similarity: match,
                            score: score
                        }));
            }
        }

    }
}