import DataCollector from '../../../DataCollector';
import Log from "../../../../technicalServices/log/Logger";

import {
  measureInEphemeralIframe,
} from '../TypographyFrameUtils';

const featureSettings = {
  configKey: 'isFontEmojiFeature',
  isDefault: false,
  shouldRunPerContext: false,
  shouldRunPerSession: true,
  shouldRun: false,
  isFrameRelated: false,
  runInUns: false,
  runInSlave: true,
  runInLean: true,
  isRunning: false,
  instance: null,
};

 const EMOJI_STYLE_RAW = Object.freeze({
  whiteSpace: 'nowrap',
  letterSpacing: 'normal',
  lineHeight: 'normal',
  textTransform: 'none',
  fontVariant: 'normal',
  fontFeatureSettings: 'normal',
  fontKerning: 'auto',
  direction: 'ltr',
  writingMode: 'horizontal-tb',
  fontSize: '16px',
});

const MEASURE_RAW = Object.freeze({
  maxFrames: 12,
  minSamples: 3,
  stableHitsTarget: 2,
  absTolPx: 0,
  relTol: 0,
  quantStep: 0,
});
export default class FontEmojiFeature extends DataCollector {
  static getDefaultSettings() { return featureSettings; }

  constructor(dataQ, window = global.window, document = global.document) {
    super();
    this._dataQ = dataQ;
    this._window = window;
    this._document = document;
  }

  _buildEmojiString() {
    // U+1F600..U+1F64F (80 code points), NFC
    return Array.from({ length: 80 }, (_, i) => String.fromCodePoint(0x1F600 + i))
      .join('')
      .normalize('NFC');
  }

  _buildSpan(doc, text, style) {
    const span = doc.createElement('span');
    try {  
        span.style.all = 'initial';  
    } catch (ex) { 
      Log.warn('applying span.style.all failed', ex); 
    }
    Object.assign(span.style, style);
    span.textContent = text;
    return span;
  }
  
  async startFeature() {
    try {
      const info = await measureInEphemeralIframe({
        hostWindow: this._window,
        hostDocument: this._document,
        frameId: 'cd__frame__font_emoji',
        buildNode: ({ doc }) => this._buildSpan(
          doc,
          this._buildEmojiString(),
          EMOJI_STYLE_RAW,
        ),
        ...MEASURE_RAW,
        });
     
      if (info) {
        this._dataQ.addToQueue('static_fields', ['font_emoji_info', info], false);
      }
    } catch (error) {
      Log.error('Failed to collect emoji font information', error);
    }
  }
}
