import Log from "../../technicalServices/log/Logger";
import StaticContract from "./StaticContract";

export default class AgenticBrowserDetectContract extends StaticContract {
    /**
     * @param {string} detectedFonts - Detected font names
     */
    constructor(detectedFonts) {
        super();
        this.detectedFonts = detectedFonts;
    }

    validateParams() {
        let isValid = (typeof this.detectedFonts === 'string');

        if (!isValid) {
            Log.warn(`wrong type in AgenticBrowserDetect parameters. detectedFonts: {expected: string, received: ${typeof this.detectedFonts}}`);
        }
    }

    /**
     * @return {[string, string]} for example: ['agentic_browser_detect', ['FontA,FontB']]
     */
    buildQueueMessage() {
        let name = this.getName();
        let message = [name, [
            this.detectedFonts
        ]];
        this.validateMessage(message);
        return message;
    }

    validateMessage(message) {
        let isValid = (
            typeof message[0] === 'string' &&
            Array.isArray(message[1]) &&
            message[1].length === 1
        );

        if (!isValid) {
            Log.warn('AgenticBrowserDetect - Contract verification failed');
        }
    }

    getName() {
        return 'agentic_browser_detect';
    }
}
