import Log from '../../../../technicalServices/log/Logger';

/**
 * WebGL Timing Service for measuring WebGL performance using uniformMatrix4fv benchmark
 */
class WebGLTimingService {
  constructor({ log, document, performance, math } = {}) {
    this._log = log || Log;
    this._document = document || window.document;
    this._performance = performance || window.performance;
    this._math = math || Math;
    this.contextTypes = ['webgl2'];
  }

  /**
   * Collects WebGL timing information using uniformMatrix4fv benchmark
   * @returns {Object} WebGL timing data
   */
  async collectWebGLTiming() {
    try {
      const canvas = this._document.createElement('canvas');
      canvas.width = 256;
      canvas.height = 128;
      
      const gl = canvas.getContext('webgl2');
      if (!gl) {
        return {};
      }

      const benchmark = await this.runUniformMatrixBenchmark(gl);
      
      return benchmark.results.totalTime;

    } catch (error) {
      this._log.debug('WebGLTimingService - Error collecting WebGL timing:', error);
      return {};
    }
  }

  /**
   * Runs the uniformMatrix4fv benchmark
   * @param {WebGLRenderingContext} gl - WebGL context
   * @returns {Object} Benchmark results
   */
  async runUniformMatrixBenchmark(gl) {
    const benchmark = {
      iterations: 1000,
      matrixCount: 15,
      results: {}
    };

    try {
      // Create matrices for testing
      const matrices = this.createTestMatrices(benchmark.matrixCount);
      
      // Get uniform location (we'll use a simple shader)
      const program = this.createSimpleShaderProgram(gl);
      if (!program) {
        throw new Error('Failed to create shader program');
      }

      gl.useProgram(program);
      const uniformLocation = gl.getUniformLocation(program, 'u_matrix');

      // Warm up
      for (let i = 0; i < 10; i++) {
        for (let j = 0; j < matrices.length; j++) {
          gl.uniformMatrix4fv(uniformLocation, false, matrices[j]);
        }
      }

      // Run benchmark
      const startTime = this._performance.now();
      
      for (let iteration = 0; iteration < benchmark.iterations; iteration++) {
        for (let matrixIndex = 0; matrixIndex < matrices.length; matrixIndex++) {
          gl.uniformMatrix4fv(uniformLocation, false, matrices[matrixIndex]);
        }
      }
      
      const endTime = this._performance.now();
      const totalTime = endTime - startTime;

      // Calculate metrics
      const totalOperations = benchmark.iterations * benchmark.matrixCount;
      const operationsPerSecond = (totalOperations / totalTime) * 1000;

      benchmark.results = {
        totalTime,
        operationsPerSecond
      };

      // Cleanup
      gl.deleteProgram(program);

    } catch (error) {
      this._log.debug('WebGLTimingService - Error running uniformMatrix benchmark:', error);
      benchmark.results = {
        error: error.message,
        totalTime: 0,
        operationsPerSecond: 0
      };
    }

    return benchmark;
  }

  /**
   * Creates test matrices for the benchmark
   * @param {number} count - Number of matrices to create
   * @returns {Array<Float32Array>} Array of test matrices
   */
  createTestMatrices(count) {
    const matrices = [];
    
    for (let i = 0; i < count; i++) {
      const matrix = new Float32Array(16);
      
      // Create a rotation matrix around different axes
      const angle = (i / count) * this._math.PI * 2;
      const cos = this._math.cos(angle);
      const sin = this._math.sin(angle);
      
      // Rotation around Y-axis
      matrix[0] = cos;  matrix[1] = 0;  matrix[2] = sin;  matrix[3] = 0;
      matrix[4] = 0;    matrix[5] = 1;  matrix[6] = 0;    matrix[7] = 0;
      matrix[8] = -sin; matrix[9] = 0;  matrix[10] = cos; matrix[11] = 0;
      matrix[12] = 0;   matrix[13] = 0; matrix[14] = 0;   matrix[15] = 1;
      
      matrices.push(matrix);
    }
    
    return matrices;
  }

  /**
   * Creates a simple shader program for the benchmark
   * @param {WebGLRenderingContext} gl - WebGL context
   * @returns {WebGLProgram|null} Shader program or null if failed
   */
  createSimpleShaderProgram(gl) {
    try {
      const vertexShaderSource = `
        attribute vec4 a_position;
        uniform mat4 u_matrix;
        void main() {
          gl_Position = u_matrix * a_position;
        }
      `;

      const fragmentShaderSource = `
        precision mediump float;
        void main() {
          gl_FragColor = vec4(1.0, 0.0, 0.0, 1.0);
        }
      `;

      const vertexShader = gl.createShader(gl.VERTEX_SHADER);
      gl.shaderSource(vertexShader, vertexShaderSource);
      gl.compileShader(vertexShader);

      if (!gl.getShaderParameter(vertexShader, gl.COMPILE_STATUS)) {
        this._log.debug('WebGLTimingService - Vertex shader compilation failed:', gl.getShaderInfoLog(vertexShader));
        gl.deleteShader(vertexShader);
        return null;
      }

      const fragmentShader = gl.createShader(gl.FRAGMENT_SHADER);
      gl.shaderSource(fragmentShader, fragmentShaderSource);
      gl.compileShader(fragmentShader);

      if (!gl.getShaderParameter(fragmentShader, gl.COMPILE_STATUS)) {
        this._log.debug('WebGLTimingService - Fragment shader compilation failed:', gl.getShaderInfoLog(fragmentShader));
        gl.deleteShader(vertexShader);
        gl.deleteShader(fragmentShader);
        return null;
      }

      const program = gl.createProgram();
      gl.attachShader(program, vertexShader);
      gl.attachShader(program, fragmentShader);
      gl.linkProgram(program);

      if (!gl.getProgramParameter(program, gl.LINK_STATUS)) {
        this._log.debug('WebGLTimingService - Program linking failed:', gl.getProgramInfoLog(program));
        gl.deleteShader(vertexShader);
        gl.deleteShader(fragmentShader);
        gl.deleteProgram(program);
        return null;
      }

      // Cleanup shaders (they're now attached to the program)
      gl.deleteShader(vertexShader);
      gl.deleteShader(fragmentShader);

      return program;
    } catch (error) {
      this._log.debug('WebGLTimingService - Error creating shader program:', error);
      return null;
    }
  }
}

export default WebGLTimingService; 