import {
    defaultCollectorsV2,
    framesRelatedCollectorsV2, nonDefaultCollectorsV2,
    perContextCollectorsV2, perSessionCollectorsV2,
    shouldRunCollectorsV2
} from "./FeaturesListV2";
import IframeDetector from "./detectors/iframes/IframesDetector";
import DataCollectorsFactory from "./factories/DataCollectorsFactory";
import Log from "../technicalServices/log/Logger";
import DependenciesSingleton from "./injectors/DependenciesContainer";
import ElementsDetector from "./detectors/elements/ElementsDetector";
import ShadowDOMsDetector from "./detectors/shadow-doms/ShadowDOMsDetector";
import {ConfigurationFields} from "../core/configuration/ConfigurationFields";
import BrowserContext from "../core/browsercontexts/BrowserContext";
import DOMUtils from "../technicalServices/DOMUtils";
import {MessageBusEventType} from "../events/MessageBusEventType";
import MutationsEmitterV2 from "./observers/MutationsEmitterV2";
import CollectorsState from "./states/CollectorsState";


export default class FeatureServiceV2 {

    _elementsDetector = new ElementsDetector();
    _shadowDOMsDetector = new ShadowDOMsDetector();
    _dataCollectorsFactory = new DataCollectorsFactory();
    _mutationEmitter = new MutationsEmitterV2();
    _collectorsState = new CollectorsState();

    // TODO: make sure that each shadow root detect iframes and vice versa
    constructor(configurationRepository, elementsCollector, msgBus) {
        this._configurationRepository = configurationRepository;
        this._iframesDetector = new IframeDetector(configurationRepository);
        this._elementsCollector = elementsCollector;
        this._msgBus = msgBus;
    }

    runDefaultCollectors() {
        this._onWindowLoaded(() => {
            this._observeWindowMutations();
            this._runCollectorsOn(document, defaultCollectorsV2);
            this._scanElementsIn(document, this._sendElementToServer);
            this._scanShadowRootsIn(document, this._observeAndScanShadowDOM);
            this._scanIframesIn(document, framesRelatedCollectorsV2);
        });
    }

    updateRunByConfig(configurationRepository) {
        const collectors = {
            ...defaultCollectorsV2,
            ...perContextCollectorsV2,
            ...nonDefaultCollectorsV2,
        }
        this._updateFeatures(collectors, configurationRepository);
    }

    runPerContextFeatures(contextData) {
        if (!this._configurationRepository.isConfigurationUpdatedFromServer()) {
            Log.info("We didn't receive configurations from the server yet. Skipping per context features start until configuration arrives.");
            return;
        }

        Log.info('running per context feature');
        this._runCollectorsOn(document, perContextCollectorsV2);

        if (contextData.name !== 'cd_auto') {
            this._scanIframesIn(document, {
                ...defaultCollectorsV2,
                ...framesRelatedCollectorsV2,
            });
        }
    }

    runPerSessionFeatures() {
        Log.info('Running per session features');

        if (!this._configurationRepository.isConfigurationUpdatedFromServer()) {
            Log.info("We didn't receive configurations from the server yet. Skipping per session features start until configuration arrives.");
            return;
        }

        // Start the per session features according to the configuration
        this._runCollectorsOn(document, perSessionCollectorsV2);
    }

    stopAllFeatures() {
        Log.info('Stopping all features');
        //this.stopNonDefault();
        const collectorsToStop = {
            ...defaultCollectorsV2,
            ...perContextCollectorsV2,
            ...perSessionCollectorsV2,
        }
        this._stopCollectors(collectorsToStop);
    }

    _runCollectorsOn(doc, collectors) {
        Object.keys(collectors).forEach(collector => {
            this._runCollector(doc, collector);
        });
    }

    _runFeaturesWithConfig(doc, collectors, configurationRepository) {
        Object.keys(collectors).forEach(collector => {
            const key = this._dataCollectorsFactory.configKey(collector);
            if (configurationRepository.get(key)) {
                this._runCollector(doc, collector);
            }
        });
    }

    _runCollector(doc, collectorName) {
        const collector = this._dataCollectorsFactory.create(collectorName);
        this._collectorsState.addCollector(doc, collectorName, collector);

        if (collector.getEvents().length === 0) {
            // TODO: refactor this code
            // TODO: meant to run static collectors
            collector.handleEvent();
        }
        collector.getEvents().forEach((eventType) => {
            doc.addEventListener(eventType, collector.handleEvent);
        })
        Log.info('Running Collector: ', collectorName, ' On: ', doc.URL);
    }

    _stopCollector(doc, collectorName) {
        try {
            const collector = this._collectorsState.getCollectorByName(doc, collectorName);
            this._collectorsState.removeCollectorForDoc(doc, collectorName);
            collector.getEvents().forEach((eventType) => {
                doc.removeEventListener(eventType, collector.handleEvent)
            })
        } catch (e) {
            //Log.log(`Error stop collector: ${e}`);
        }
    }

    _stopCollectors(collectors) {
        Object.keys(collectors).forEach(collector => {
            this._collectorsState.forEachDoc((doc) => {
                this._stopCollector(doc, collector);
            });
        })
    }

    _updateFeatures(features, configurationRepository) {
        Object.keys(features).forEach(collector => {
            const collectorShouldRun = this._collectorShouldRun(collector, configurationRepository);
            const collectorRunning = this._collectorsState.collectorExistsInAnyDoc(collector);
            if(collectorRunning && !collectorShouldRun) {
                this._collectorsState.forEachDoc((doc) => {
                    this._stopCollector(doc, collector);
                });
            } else if(!collectorRunning && collectorShouldRun ) {
                this._collectorsState.forEachDoc((doc) => {
                    this._runCollector(doc, collector);
                });
            } else if (collectorRunning) {
                this._collectorsState.forEachDoc((doc) => {
                    this._updateCollector(doc, collector);
                });
            }
        });
    }

    _collectorShouldRun(collector, configurationRepository) {
        const key = this._dataCollectorsFactory.configKey(collector);
        const config = configurationRepository.get(key);
        let collectorShouldRun = true;
        if(config === undefined) {
            collectorShouldRun = true;
        } else if (typeof config === 'boolean' ) {
            collectorShouldRun = config;
        }
        return collectorShouldRun;
    }

    _updateCollector(doc, collectorName) {
        try {
            const collector = this._collectorsState.getCollectorByName(doc, collectorName);
            collector.updateConfig();
        } catch (e) {
            //Log.log(`Tried to update collector ${e}`);
        }
    }

    _scanIframesIn(doc, collectors) {
        this._iframesDetector.detect(doc, (iframe) => {
            this._observeMutationsIn(iframe.body);
            this._runCollectorsOn(iframe, collectors);
            this._scanElementsIn(iframe, this._sendElementToServer);
        });
    }

    _onMutation = (evt) => {
        const node = evt && evt.node;
        if (!node) return;
        if (evt.type === 'added') {
            if (this._isShadowRoot(node)) {
                this._scanElementsIn(node.shadowRoot, this._sendElementToServer);
            }
            if (this._isIframe(node)) {
                if (node.contentDocument) {
                    // Iframe is Loaded
                    this._observeMutationsIn(node.contentDocument.body);
                } else {
                    const onLoad = () => {
                        this._observeMutationsIn(node.contentDocument.body);
                    };
                    node.addEventListener('load', onLoad, { once: true });
                }
                this._runCollectorsAndScanElementsInIframe(node);
            }
        } else if (evt.type === 'removed') {
            if (this._isIframe(node)) {
                this._cleanupIframe(node);
            }
        }
    }

    _isIframe(node) {
       return node.nodeType === Node.ELEMENT_NODE && node.nodeName === 'IFRAME';
    }

    _runCollectorsAndScanElementsInIframe(node) {
        const onLoad = () => {
            const iframe = node.contentDocument || node.contentWindow?.document;
            this._iframesDetector.addIframeToCache(iframe); // TODO: figure out the depth of the iframe?
            this._runCollectorsOn(iframe, framesRelatedCollectorsV2);
            this._scanElementsIn(iframe, this._sendElementToServer);
        };
        node.addEventListener('load', onLoad, { once: true });
    }

    _isShadowRoot(node) {
        const shadowRoot = node.shadowRoot;
        return shadowRoot && shadowRoot.mode === 'open';
    }

    _observeAndScanShadowDOM = (shadowDOM) => {
        this._observeMutationsIn(shadowDOM);
        this._scanElementsIn(shadowDOM, this._sendElementToServer);
    }

    _observeMutationsIn(element) {
        if (!element) return;
        this._mutationEmitter.observeElement(element, this._onMutation);
    }

    _observeWindowMutations() {
        if (!this._configurationRepository.get(ConfigurationFields.isMutationObserver)) return;
        this._mutationEmitter.observe(window, this._onMutation);
        //this._jQueryElementListenerSiteMapper.initTracking();
    }

    _sendElementToServer = (element) => {
        this._elementsCollector.getElement(element); // Sending the element to the server;
    }

    _scanElementsIn(rootNode, onElement) {
        this._elementsDetector.detect(rootNode, onElement);
    }

    _scanShadowRootsIn(rootNode, onShadowRoot) {
        this._shadowDOMsDetector.detect(rootNode, onShadowRoot);
    }

    _onWindowLoaded(func) {
        if (document.readyState === 'complete') {
            func();
            return;
        }
        window.addEventListener('load', () => {
            func();
        }, {once: true});
    }

    _cleanupIframe(iframeElement) {
        try {
            const iframeDoc = iframeElement.contentDocument || iframeElement.contentWindow?.document;
            if (!iframeDoc) return;
            this._cleanupDocument(iframeDoc);
        } catch (e) {
            // Cross-origin or already detached; best-effort cleanup
        }
    }

    _cleanupDocument(doc) {
        const collectorNames = [];
        this._collectorsState.forEachCollectorNameInDoc(doc, (name) => collectorNames.push(name));
        collectorNames.forEach((name) => this._stopCollector(doc, name));
        this._collectorsState.removeDoc(doc);
        try {
            if (doc && doc.body) {
                this._mutationEmitter.unobserve(doc.body);
            }
        } catch (e) {
            // ignore
        }
        try {
            this._iframesDetector.removeIframeFromCache(doc);
        } catch (e) {
            // ignore
        }
        Log.info('Cleaned up document:', doc?.URL);
    }
}