const DEFAULT_FEATURE_SETTINGS = {
    configKey: 'isAudioDetectFeature2',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: false,
    shouldRun: false,
    isFrameRelated: false,
    runInUns: false,
    runInSlave: false,
    runInLean: false,
    isRunning: false,
    instance: null,
};

export default class AudioDetectFeature2 extends DataCollector {
    static getDefaultSettings() {
        return { ...DEFAULT_FEATURE_SETTINGS };
    }

    /**
     * @param {DataQueue} dataQ
     * @param {Utils} utils
     * @param {Object} options
     * @param {Object} options.audioContext - AudioContext constructors
     * @param {Function} options.audioContext.regular - Regular AudioContext constructor
     * @param {Function} options.audioContext.offline - OfflineAudioContext constructor
     * @param {Object} options.performance - Performance API
     * @param {Object} options.audioConfig - Optional custom audio config
     * @param {Object} options.featureSettings - Optional custom feature settings
     */
    constructor(dataQ, utils, options = {}) {
        super();
        
        if (!dataQ) throw new Error('DataQueue is required');
        if (!utils) throw new Error('Utils is required');
        
        this._dataQ = dataQ;
        this._utils = utils;
        
        // Inject AudioContext constructors
        const audioContext = options.audioContext || {};
        this._AudioContext = audioContext.regular || (window.AudioContext || window.webkitAudioContext);
        this._OfflineAudioContext = audioContext.offline || (window.OfflineAudioContext || window.webkitOfflineAudioContext);
        
        if (!this._AudioContext || !this._OfflineAudioContext) {
            throw new Error('AudioContext constructors are required');
        }
        
        // Inject performance API
        this._performance = options.performance || window.performance;
        if (!this._performance) {
            throw new Error('Performance API is required');
        }
        
        // Use provided configs or defaults
        this._audioConfig = options.audioConfig || { ...DEFAULT_AUDIO_CONFIG };
        this._featureSettings = options.featureSettings || { ...DEFAULT_FEATURE_SETTINGS };
    }

    startFeature() {
        Log.info("Starting Advanced Audio Detection Feature 2");
        try {
            this._collectAudioFingerprintInfo();
        } catch (error) {
            Log.error(`Failed to collect audio information: ${error}`);
        }
    }

    _collectAudioFingerprintInfo() {
        Log.info('Collecting advanced audio properties (method 2)');
        this._getAdvancedAudioFingerprint()
            .then((audioData) => {
                return this._utils.digest_sha256(audioData.toString());
            })
            .then((hash) => {
                this._dataQ.addToQueue('static_fields', ['audio_detect2', hash], false);
                Log.info('Advanced audio properties collected successfully (method 2) ' + hash);
            }).catch((error) => {
            Log.error(`Error collecting audio information: ${error}`);
        });
    }

    /**
     * Generates an advanced audio fingerprint using multiple techniques.
     * 
     * @returns {Promise<number>} - Resolves with the stabilized audio fingerprint value.
     * @throws Error if the AudioContext initialization or rendering fails.
     */
    async _getAdvancedAudioFingerprint() {
        try {
            // Get browser-specific audio context info
            const contextInfo = await this._getAudioContextInfo();
            
            // Generate multiple signals with different characteristics
            const signals = await Promise.all([
                this._getBaseSignal(this._OfflineAudioContext), // Original FingerprintJS signal
                ...this._audioConfig.signals.map(config => this._getAdvancedSignal(config))
            ]);

            // Process each signal and collect advanced metrics
            const processedResults = [];
            for (const signal of signals) {
                if (signal) {
                    const processed = await this._applyAdvancedProcessing(signal);
                    if (processed && processed.buffer && processed.metrics) {
                        processedResults.push({
                            buffer: processed.buffer,
                            metrics: processed.metrics
                        });
                    }
                }
            }

            if (processedResults.length === 0) {
                throw new Error("No valid processed signals");
            }

            // Combine signals with advanced mixing
            const combinedSignal = await this._combineSignalsAdvanced(processedResults.map(p => p.buffer));

            // Generate composite fingerprint from multiple sources
            const fingerprint = this._generateCompositeFingerprint(
                combinedSignal,
                processedResults.map(p => p.metrics),
                contextInfo
            );

            // Apply advanced stabilization
            const stabilizedFingerprint = this._stabilizeAdvanced(fingerprint, contextInfo);
            return this._stabilize(stabilizedFingerprint, this._audioConfig.stabilizationPrecision);
        } catch (error) {
            Log.error(`Error in advanced audio fingerprinting: ${error}`);
            throw error;
        }
    }

    /**
     * Gets comprehensive audio context information
     */
    async _getAudioContextInfo() {
        const regularContext = new this._AudioContext();
        
        const info = {
            sampleRate: regularContext.sampleRate,
            baseLatency: regularContext.baseLatency || 0,
            maxChannelCount: regularContext.destination.maxChannelCount,
            state: regularContext.state,
            supportedSampleRates: [],
            supportedChannelCounts: [],
            browser: this._browserInfo
        };

        // Test different sample rates
        for (const rate of [8000, 11025, 16000, 22050, 24000, 32000, 44100, 48000, 88200, 96000]) {
            try {
                const testContext = new this._AudioContext({ sampleRate: rate });
                info.supportedSampleRates.push(rate);
                if (typeof testContext.close === 'function') {
                    testContext.close();
                }
            } catch (e) {}
        }

        // Test different channel counts
        for (let channels = 1; channels <= 32; channels *= 2) {
            try {
                const testContext = new this._AudioContext();
                const merger = testContext.createChannelMerger(channels);
                info.supportedChannelCounts.push(channels);
                if (typeof testContext.close === 'function') {
                    testContext.close();
                }
            } catch (e) {}
        }

        if (typeof regularContext.close === 'function') {
            regularContext.close();
        }
        return info;
    }

    /**
     * Generates the original FingerprintJS base signal
     */
    async _getBaseSignal(AudioContext) {
        const context = new AudioContext(1, this._audioConfig.targetSampleIndex + 1, this._audioConfig.sampleRate);

        const oscillator = context.createOscillator();
        oscillator.type = "square";
        oscillator.frequency.value = 1000;

        const compressor = context.createDynamicsCompressor();
        compressor.threshold.value = -70;
        compressor.knee.value = 40;
        compressor.ratio.value = 12;
        compressor.attack.value = 0;
        compressor.release.value = 0.25;

        const filter = context.createBiquadFilter();
        filter.type = "allpass";
        filter.frequency.value = 5.239622852977861;
        filter.Q.value = 0.1;

        oscillator.connect(compressor);
        compressor.connect(filter);
        filter.connect(context.destination);
        oscillator.start(0);

        return await this._renderAudio(context);
    }

    /**
     * Generates an advanced signal with complex characteristics
     */
    async _getAdvancedSignal(signalConfig) {
        const context = new this._OfflineAudioContext(
            1,
            this._audioConfig.targetSampleIndex + 1,
            this._audioConfig.sampleRate
        );

        const oscillator = context.createOscillator();
        oscillator.type = signalConfig.type;
        oscillator.frequency.value = signalConfig.frequency;
        
        if (signalConfig.detune !== 0) {
            oscillator.detune.value = signalConfig.detune;
        }

        // Create advanced processing chain
        const compressor = this._createAdvancedCompressor(context);
        const filters = this._createFilterChain(context);

        // Connect oscillator through processing chain
        let currentNode = oscillator;
        currentNode.connect(compressor);
        currentNode = compressor;

        // Connect through filters
        filters.forEach(filter => {
            currentNode.connect(filter);
            currentNode = filter;
        });

        // Add harmonics if specified
        if (signalConfig.harmonics) {
            const harmonicMixer = context.createGain();
            currentNode.connect(harmonicMixer);
            
            signalConfig.harmonics.forEach(harmonic => {
                const harmonicOsc = context.createOscillator();
                harmonicOsc.type = 'sine';
                harmonicOsc.frequency.value = harmonic.frequency;
                harmonicOsc.connect(harmonicMixer, 0, 0);
                harmonicMixer.gain.value = harmonic.amplitude;
                harmonicOsc.start(0);
            });
            
            currentNode = harmonicMixer;
        }

        // Add modulation if specified
        if (signalConfig.modulation) {
            const modulator = context.createOscillator();
            modulator.type = 'sine';
            modulator.frequency.value = signalConfig.modulation.frequency;
            
            const modulationGain = context.createGain();
            modulationGain.gain.value = signalConfig.modulation.depth;
            
            modulator.connect(modulationGain);
            modulationGain.connect(oscillator.frequency);
            modulator.start(0);
        }

        // Add formants if specified
        if (signalConfig.formants) {
            signalConfig.formants.forEach(formant => {
                const formantFilter = context.createBiquadFilter();
                formantFilter.type = 'bandpass';
                formantFilter.frequency.value = formant.frequency;
                formantFilter.Q.value = formant.frequency / formant.bandwidth;
                
                currentNode.connect(formantFilter);
                currentNode = formantFilter;
            });
        }

        // Add resonance if specified
        if (signalConfig.resonance) {
            const resonanceFilter = context.createBiquadFilter();
            resonanceFilter.type = 'peaking';
            resonanceFilter.frequency.value = signalConfig.resonance.frequency;
            resonanceFilter.Q.value = signalConfig.resonance.Q;
            resonanceFilter.gain.value = 6;
            
            currentNode.connect(resonanceFilter);
            currentNode = resonanceFilter;
        }

        currentNode.connect(context.destination);
        oscillator.start(0);

        return await this._renderAudio(context);
    }

    /**
     * Creates an advanced compressor with browser-specific optimizations
     */
    _createAdvancedCompressor(context) {
        const compressor = context.createDynamicsCompressor();
        const config = this._audioConfig.compressor;
        
        compressor.threshold.value = config.threshold;
        compressor.knee.value = config.knee;
        compressor.ratio.value = config.ratio;
        compressor.attack.value = config.attack;
        compressor.release.value = config.release;
        
        if (compressor.makeupGain) {
            compressor.makeupGain.value = config.makeupGain;
        }
        
        return compressor;
    }

    /**
     * Creates a chain of filters for advanced processing
     */
    _createFilterChain(context) {
        return this._audioConfig.filters.map(filterConfig => {
            const filter = context.createBiquadFilter();
            filter.type = filterConfig.type;
            filter.frequency.value = filterConfig.frequency;
            filter.Q.value = filterConfig.Q;
            filter.gain.value = filterConfig.gain;
            return filter;
        });
    }

    /**
     * Applies advanced processing to a signal and extracts metrics
     */
    async _applyAdvancedProcessing(signal) {
        const context = new this._AudioContext();
        
        if (!(signal instanceof AudioBuffer)) {
            throw new Error('Invalid audio signal: expected AudioBuffer');
        }

        // Create analyser for spectral analysis
        const analyser = context.createAnalyser();
        analyser.fftSize = this._audioConfig.analysis.fftSize;
        analyser.smoothingTimeConstant = this._audioConfig.analysis.smoothingTimeConstant;
        analyser.minDecibels = this._audioConfig.analysis.minDecibels;
        analyser.maxDecibels = this._audioConfig.analysis.maxDecibels;

        // Create buffer source
        const source = context.createBufferSource();
        source.buffer = signal;

        // Connect for analysis
        source.connect(analyser);
        analyser.connect(context.destination);
        source.start(0);

        // Collect analysis data
        const spectralData = new Float32Array(analyser.frequencyBinCount);
        const timeData = new Float32Array(analyser.frequencyBinCount);
        
        analyser.getFloatFrequencyData(spectralData);
        analyser.getFloatTimeDomainData(timeData);

        // Calculate advanced metrics
        const metrics = {
            spectral: this._analyzeSpectralData(spectralData),
            phase: this._analyzePhaseData(timeData),
            dynamic: this._analyzeDynamicRange(spectralData),
            harmonic: this._analyzeHarmonicContent(spectralData, timeData),
            temporal: this._analyzeTemporalCharacteristics(timeData)
        };

        return {
            buffer: signal,
            metrics: metrics
        };
    }

    /**
     * Analyzes spectral data for fingerprinting
     */
    _analyzeSpectralData(spectralData) {
        const metrics = {
            peaks: [],
            centroid: 0,
            spread: 0,
            skewness: 0,
            kurtosis: 0
        };

        if (!spectralData || spectralData.length === 0) {
            return metrics;
        }

        // Find spectral peaks
        for (let i = 1; i < spectralData.length - 1; i++) {
            if (spectralData[i] > spectralData[i-1] && spectralData[i] > spectralData[i+1]) {
                metrics.peaks.push({ index: i, magnitude: spectralData[i] });
            }
        }

        // Sort peaks by magnitude and limit count
        metrics.peaks.sort((a, b) => b.magnitude - a.magnitude);
        metrics.peaks = metrics.peaks.slice(0, this._audioConfig.analysis.spectralPeaks);

        // Calculate spectral centroid
        let numerator = 0;
        let denominator = 0;
        for (let i = 0; i < spectralData.length; i++) {
            const magnitude = Math.pow(10, spectralData[i] / 20);
            numerator += i * magnitude;
            denominator += magnitude;
        }
        
        if (denominator > 0) {
            metrics.centroid = numerator / denominator;
        }

        // Calculate spectral spread
        if (denominator > 0) {
            let spreadSum = 0;
            for (let i = 0; i < spectralData.length; i++) {
                const magnitude = Math.pow(10, spectralData[i] / 20);
                spreadSum += Math.pow(i - metrics.centroid, 2) * magnitude;
            }
            metrics.spread = Math.sqrt(spreadSum / denominator);
        }

        return metrics;
    }

    /**
     * Analyzes phase data for fingerprinting
     */
    _analyzePhaseData(timeData) {
        const metrics = {
            zeroCrossings: 0,
            phaseVariance: 0,
            phaseCoherence: 0
        };

        if (!timeData || timeData.length === 0) {
            return metrics;
        }

        // Count zero crossings
        let prevSample = timeData[0];
        for (let i = 1; i < timeData.length; i++) {
            if ((prevSample < 0 && timeData[i] >= 0) || (prevSample >= 0 && timeData[i] < 0)) {
                metrics.zeroCrossings++;
            }
            prevSample = timeData[i];
        }

        // Calculate phase variance
        const mean = timeData.reduce((a, b) => a + b, 0) / timeData.length;
        metrics.phaseVariance = timeData.reduce((a, b) => a + Math.pow(b - mean, 2), 0) / timeData.length;

        // Calculate phase coherence
        if (timeData.length > 1) {
            let coherenceSum = 0;
            for (let i = 1; i < timeData.length; i++) {
                const phaseDiff = Math.abs(timeData[i] - timeData[i-1]);
                coherenceSum += Math.cos(phaseDiff);
            }
            metrics.phaseCoherence = coherenceSum / (timeData.length - 1);
        }

        return metrics;
    }

    /**
     * Analyzes dynamic range characteristics
     */
    _analyzeDynamicRange(spectralData) {
        const metrics = {
            crestFactor: 0,
            dynamicRange: 0,
            compressionRatio: 0
        };

        if (!spectralData || spectralData.length === 0) {
            return metrics;
        }

        const linearData = spectralData.map(x => Math.pow(10, x / 20)).filter(x => x > 0);
        
        if (linearData.length === 0) {
            return metrics;
        }
        
        const rms = Math.sqrt(linearData.reduce((a, b) => a + b * b, 0) / linearData.length);
        const peak = Math.max(...linearData);
        
        if (rms > 0) {
            metrics.crestFactor = peak / rms;
            const min = Math.min(...linearData);
            metrics.dynamicRange = 20 * Math.log10(peak / min);
        }
        
        return metrics;
    }

    /**
     * Analyzes harmonic content
     */
    _analyzeHarmonicContent(spectralData, timeData) {
        const metrics = {
            totalHarmonicDistortion: 0,
            harmonicSpectrum: 0
        };

        if (!spectralData || spectralData.length === 0) {
            return metrics;
        }

        const linearData = spectralData.map(x => Math.pow(10, x / 20));
        const fundamental = Math.max(...linearData);
        const harmonics = linearData.filter((value, index) => index > 0 && value > fundamental * 0.1);
        const harmonicSum = harmonics.reduce((sum, value) => sum + value * value, 0);
        
        metrics.totalHarmonicDistortion = fundamental > 0 ? Math.sqrt(harmonicSum) / fundamental : 0;

        return metrics;
    }

    /**
     * Analyzes temporal characteristics
     */
    _analyzeTemporalCharacteristics(timeData) {
        const metrics = {
            attackTime: 0,
            decayTime: 0,
            sustainLevel: 0
        };

        if (!timeData || timeData.length === 0) {
            return metrics;
        }

        const envelope = this._calculateEnvelope(timeData);
        
        if (envelope.length > 0) {
            const peak = Math.max(...envelope);
            const attackThreshold = peak * 0.9;
            let attackIndex = 0;
            
            for (let i = 0; i < envelope.length; i++) {
                if (envelope[i] >= attackThreshold) {
                    attackIndex = i;
                    break;
                }
            }
            metrics.attackTime = attackIndex / this._audioConfig.sampleRate;
        }

        return metrics;
    }

    /**
     * Calculates signal envelope
     */
    _calculateEnvelope(timeData) {
        const envelope = [];
        const windowSize = Math.max(1, Math.floor(timeData.length * 0.001));
        
        for (let i = 0; i < timeData.length; i += windowSize) {
            const window = timeData.slice(i, Math.min(i + windowSize, timeData.length));
            const rms = Math.sqrt(window.reduce((sum, sample) => sum + sample * sample, 0) / window.length);
            envelope.push(rms);
        }
        
        return envelope;
    }

    /**
     * Combines multiple signals with advanced mixing
     */
    async _combineSignalsAdvanced(signals) {
        if (!Array.isArray(signals) || signals.length === 0) {
            throw new Error('Invalid signals array');
        }

        const context = new this._OfflineAudioContext(
            1,
            this._audioConfig.targetSampleIndex + 1,
            this._audioConfig.sampleRate
        );

        const combinedBuffer = context.createBuffer(1, context.length, context.sampleRate);
        const combinedData = combinedBuffer.getChannelData(0);

        // Use weighted mixing based on signal characteristics
        const weights = signals.map((_, index) => 1.0 - (index * 0.1));
        const totalWeight = weights.reduce((sum, weight) => sum + weight, 0);

        for (let i = 0; i < combinedData.length; i++) {
            let weightedSum = 0;
            for (let j = 0; j < signals.length; j++) {
                const signal = signals[j];
                const channelData = signal.getChannelData(0);
                if (i < channelData.length) {
                    weightedSum += channelData[i] * weights[j];
                }
            }
            combinedData[i] = weightedSum / totalWeight;
        }

        return combinedBuffer;
    }

    /**
     * Generates composite fingerprint from multiple sources
     */
    _generateCompositeFingerprint(signal, metricsArray, contextInfo) {
        // Hash the combined signal
        const signalHash = this._hashSignal(signal.getChannelData(0));
        
        // Hash metrics from all processed signals
        const metricsHash = metricsArray.reduce((hash, metrics, index) => {
            const frequencyWeight = 1.0 + (index * 0.1);
            
            const spectralHash = this._hashSpectralMetrics(metrics.spectral || {});
            const phaseHash = this._hashPhaseMetrics(metrics.phase || {});
            const dynamicHash = this._hashDynamicMetrics(metrics.dynamic || {});
            const harmonicHash = this._hashHarmonicMetrics(metrics.harmonic || {});
            const temporalHash = this._hashTemporalMetrics(metrics.temporal || {});
            
            const combinedMetricsHash = this._combineHashes([
                spectralHash, phaseHash, dynamicHash, harmonicHash, temporalHash
            ]);
            
            const weightedHash = Math.floor(combinedMetricsHash * frequencyWeight);
            return this._combineHashes([hash, weightedHash]);
        }, 0);

        // Combine with context info
        const contextHash = this._hashContextInfo(contextInfo);
        
        return this._combineHashes([signalHash, metricsHash, contextHash]);
    }

    /**
     * Hashes signal data
     */
    _hashSignal(signalData) {
        if (!signalData || !signalData.length) {
            return 0;
        }

        let hash = 0;
        const intervals = [1, 10, 50, 100];
        
        intervals.forEach(interval => {
            for (let i = 0; i < signalData.length; i += interval) {
                const sample = signalData[i] || 0;
                const positionWeight = 1.0 + (i / signalData.length) * 0.5;
                const weightedSample = sample * positionWeight;
                hash = ((hash << 5) - hash) + weightedSample;
                hash = hash & hash;
            }
        });
        
        return hash;
    }

    /**
     * Hashes spectral metrics
     */
    _hashSpectralMetrics(spectralData) {
        let hash = 0;
        
        if (Array.isArray(spectralData.peaks)) {
            spectralData.peaks.forEach(peak => {
                if (peak && typeof peak === 'object') {
                    hash = ((hash << 5) - hash) + (peak.index || 0);
                    hash = ((hash << 5) - hash) + (peak.magnitude || 0);
                }
            });
        }

        hash = ((hash << 5) - hash) + (spectralData.centroid || 0);
        hash = ((hash << 5) - hash) + (spectralData.spread || 0);
        hash = ((hash << 5) - hash) + (spectralData.skewness || 0);
        hash = ((hash << 5) - hash) + (spectralData.kurtosis || 0);

        return hash & hash;
    }

    /**
     * Hashes phase metrics
     */
    _hashPhaseMetrics(phaseData) {
        let hash = 0;
        hash = ((hash << 5) - hash) + (phaseData.zeroCrossings || 0);
        hash = ((hash << 5) - hash) + (phaseData.phaseVariance || 0);
        hash = ((hash << 5) - hash) + (phaseData.phaseCoherence || 0);
        return hash & hash;
    }

    /**
     * Hashes dynamic metrics
     */
    _hashDynamicMetrics(metrics) {
        let hash = 0;
        hash = ((hash << 5) - hash) + (metrics.crestFactor || 0);
        hash = ((hash << 5) - hash) + (metrics.dynamicRange || 0);
        hash = ((hash << 5) - hash) + (metrics.compressionRatio || 0);
        return hash & hash;
    }

    /**
     * Hashes harmonic metrics
     */
    _hashHarmonicMetrics(metrics) {
        let hash = 0;
        hash = ((hash << 5) - hash) + (metrics.totalHarmonicDistortion || 0);
        hash = ((hash << 5) - hash) + (metrics.harmonicSpectrum || 0);
        return hash & hash;
    }

    /**
     * Hashes temporal metrics
     */
    _hashTemporalMetrics(metrics) {
        let hash = 0;
        hash = ((hash << 5) - hash) + (metrics.attackTime || 0);
        hash = ((hash << 5) - hash) + (metrics.decayTime || 0);
        hash = ((hash << 5) - hash) + (metrics.sustainLevel || 0);
        return hash & hash;
    }

    /**
     * Hashes context information
     */
    _hashContextInfo(contextInfo) {
        let hash = 0;
        hash = ((hash << 5) - hash) + contextInfo.sampleRate;
        hash = ((hash << 5) - hash) + contextInfo.baseLatency;
        hash = ((hash << 5) - hash) + contextInfo.maxChannelCount;
        hash = ((hash << 5) - hash) + this._hashString(contextInfo.state);
        hash = ((hash << 5) - hash) + this._hashString(contextInfo.browser);
        
        contextInfo.supportedSampleRates.forEach(rate => {
            hash = ((hash << 5) - hash) + rate;
        });
        
        contextInfo.supportedChannelCounts.forEach(channels => {
            hash = ((hash << 5) - hash) + channels;
        });
        
        return hash & hash;
    }

    /**
     * Hashes a string
     */
    _hashString(str) {
        let hash = 0;
        if (typeof str !== 'string') {
            str = String(str);
        }
        for (let i = 0; i < str.length; i++) {
            const char = str.charCodeAt(i);
            hash = ((hash << 5) - hash) + char;
            hash = hash & hash;
        }
        return hash;
    }

    /**
     * Combines multiple hashes
     */
    _combineHashes(hashes) {
        return hashes.reduce((acc, hash) => {
            acc = ((acc << 5) - acc) + hash;
            return acc & acc;
        }, 0);
    }

    /**
     * Applies advanced stabilization
     */
    _stabilizeAdvanced(fingerprint, contextInfo) {
        const contextHash = this._hashContextInfo(contextInfo);
        const browserQuirksHash = this._hashBrowserQuirks(contextInfo);
        
        let hash = fingerprint;
        hash = ((hash << 5) - hash) + contextHash;
        hash = ((hash << 5) - hash) + browserQuirksHash;
        hash = hash & hash;
        
        return hash;
    }

    /**
     * Hashes browser-specific quirks
     */
    _hashBrowserQuirks(contextInfo) {
        let hash = 0;
        hash = ((hash << 5) - hash) + this._hashString(contextInfo.browser);
        hash = ((hash << 5) - hash) + contextInfo.baseLatency;
        hash = ((hash << 5) - hash) + contextInfo.maxChannelCount;
        return hash & hash;
    }

    /**
     * Truncates some digits of the number to make it stable.
     * `precision` is the number of significant digits to keep. The number may be not integer:
     *  - If it ends with `.2`, the last digit is rounded to the nearest multiple of 5;
     *  - If it ends with `.5`, the last digit is rounded to the nearest even number;
     */
    _stabilize(value, precision) {
        if (value === 0) {
            return value;
        }

        const power = Math.floor(Math.log10(Math.abs(value)));
        const precisionPower = power - Math.floor(precision) + 1;
        const precisionBase = 10 ** -precisionPower * ((precision * 10) % 10 || 1);
        return Math.round(value * precisionBase) / precisionBase;
    }

    /**
     * Renders audio from an OfflineAudioContext with browser-specific optimizations.
     */
    _renderAudio(audioContext) {
        return new Promise((resolve, reject) => {
            let retryCount = 0;
            const maxRetries = this._audioConfig.browserOptimizations[this._browserInfo]?.retryAttempts || 3;

            const attemptRender = () => {
                try {
                    let onComplete = () => {
                        resolve(audioBuffer);
                    };

                    audioContext.oncomplete = (e) => {
                        resolve(e.renderedBuffer);
                    };
                    
                    audioContext.onstatechange = () => {
                        if (audioContext.state === "suspended") {
                            audioContext.resume();
                        }
                    };

                    let audioBuffer = audioContext.startRendering();
                    if (audioBuffer && audioBuffer instanceof Promise) {
                        return audioBuffer.then(onComplete).catch(reject);
                    }
                } catch (error) {
                    if (++retryCount < maxRetries) {
                        setTimeout(attemptRender, 100);
                    } else {
                        reject(error);
                    }
                }
            };
            
            attemptRender();
        });
    }
} 
