import Log from "../../../main/technicalServices/log/Logger";
import NativeFontDetector from "./NativeFontDetector";
import CanvasFontDetector from "./CanvasFontDetector";

export default class FontDetectorProvider {
    constructor() {
        this._logsTag = `[FONTS-COLLECTOR]`;
    }

    /**
     * Creates a detector instance based on the specified type
     * @param {string} detectorType - The type of detector to create ('native', 'canvas')
     * @param {Object} config - Configuration repository to check constraints
     * @returns {FontDetector} The detector instance
     */
    getDetectorByType(detectorType, config = null) {
        const type = detectorType.toLowerCase();
        
        switch (type) {
            case 'native':
                return new NativeFontDetector();
            case 'canvas':
                return new CanvasFontDetector();
            default:
                throw new Error(`Invalid detector type: '${detectorType}'. Supported types: 'native', 'canvas'`);
        }
    }

    /**
     * Provides the best font detector based on the current environment
     * @param {Object} config - Configuration repository
     * @returns {FontDetector} An instance of the best available font detector
     */
    getBestDetector(config) {
        // Try native detector first
        try {
            const nativeDetector = new NativeFontDetector();
            if (nativeDetector.isSupported()) {
                Log.info(`${this._logsTag} Using native detector`);
                return nativeDetector;
            }
        } catch (error) {
            Log.error(`${this._logsTag} Native detector failed:`, error);
        }

        // Try canvas detector (fallback)
        try {
            const canvasDetector = new CanvasFontDetector();
            if (canvasDetector.isSupported()) {
                Log.info(`${this._logsTag} Using canvas detector`);
                return canvasDetector;
            }
        } catch (error) {
            Log.error(`${this._logsTag} Canvas detector failed:`, error);
        }

        throw new Error('[FontDetectorProvider] No suitable font detection implementation is supported in this environment.');
    }
}