import DataCollector from '../DataCollector';
import { ConfigurationFields } from '../../core/configuration/ConfigurationFields';
import Log from '../../technicalServices/log/Logger';

const featureSettings = {
    configKey: 'isMainThreadBlocking',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: false,
    shouldRun: false,
    isFrameRelated: false,
    runInSlave: false,
    runInLean: true,
    runInUns: false,
    isRunning: false,
    instance: null,
};

export const BlockingEventType = {
    mainThreadBlocked: 0,
};

const DEFAULT_BLOCKING_THRESHOLD = 200;

export const EventStructure = [
    'eventSequence', 'timestamp', 'eventType', 'blockingDuration', 'attribution'
];

export default class MainThreadBlockingCollector extends DataCollector {
    static getDefaultSettings() {
        return featureSettings;
    }

    constructor(configurationRepository, utils, dataQueue) {
        super();
        this._configurationRepository = configurationRepository;
        this._utils = utils;
        this._dataQueue = dataQueue;
        this._blockingThreshold = DEFAULT_BLOCKING_THRESHOLD;
        this._performanceObserver = null;
    }

    startFeature() {
        if (!this._isPerformanceObserverSupported()) {
            Log.trace('MainThreadBlocking: PerformanceObserver not supported, feature disabled');
            return;
        }

        this._performanceObserver = new PerformanceObserver((list) => {
            list.getEntries().forEach(entry => {
                if (entry.entryType === 'longtask' && entry.duration >= this._blockingThreshold) {
                    this._onBlocking(entry);
                }
            });
        });

        this._performanceObserver.observe({ entryTypes: ['longtask'] });
        Log.trace('MainThreadBlocking: Feature started using PerformanceObserver');
    }

    stopFeature() {
        if (this._performanceObserver) {
            this._performanceObserver.disconnect();
            this._performanceObserver = null;
        }
        Log.trace('MainThreadBlocking: Feature stopped');
    }

    updateFeatureConfig() {
        const newThreshold = this._configurationRepository.get(ConfigurationFields.mainThreadBlockingThreshold) || DEFAULT_BLOCKING_THRESHOLD;
        if (newThreshold !== this._blockingThreshold) {
            this._blockingThreshold = newThreshold;
            Log.trace(`MainThreadBlocking: Threshold updated to ${this._blockingThreshold}ms`);
        }
    }

    _isPerformanceObserverSupported() {
        return 'PerformanceObserver' in window && 
               PerformanceObserver.supportedEntryTypes?.includes('longtask');
    }

    _extractAttribution(entry) {
        let attribution = null;

        // Extract attribution data if available
        if (entry.attribution && entry.attribution.length > 0) {
            const firstAttribution = entry.attribution[0];
            
            // Combine script URL and line into "url/line" format
            if (firstAttribution.scriptURL && firstAttribution.scriptLine) {
                attribution = `${firstAttribution.scriptURL}/${firstAttribution.scriptLine}`;
            } else if (firstAttribution.scriptURL) {
                attribution = firstAttribution.scriptURL;
            }
        }

        return attribution;
    }

    _onBlocking(entry) {
        const eventSequence = this._utils.StorageUtils.getAndUpdateEventSequenceNumber();
        const timestamp = this.getEventTimestamp({ timeStamp: performance.now() });
        const attribution = this._extractAttribution(entry);

        Log.debug(`MainThreadBlocking: Blocking detected - ${entry.duration.toFixed(2)}ms, attribution: ${attribution}`);
        
        this._dataQueue.addToQueue('main_thread_blocking',
            this._utils.convertToArrayByMap(EventStructure, {
                eventType: BlockingEventType.mainThreadBlocked,
                eventSequence,
                timestamp,
                blockingDuration: entry.duration,
                attribution: attribution
            }));
    }
} 
