

export default class CollectorsState {


    _runningCollectorsPerDoc = new Map();

    addCollector(doc, collectorName, collector) {
        const runningCollectors = this._runningCollectorsPerDoc.get(doc) || new Map();
        runningCollectors.set(collectorName, collector);
        this._runningCollectorsPerDoc.set(doc, runningCollectors);
    }

    forEachDoc(handle) {
        this._runningCollectorsPerDoc.forEach((collectors, doc) => {
            handle(doc);
        });
    }

    forEachSavedCollector(callback) {
        this._runningCollectorsPerDoc.forEach((collectors, doc) => {
            collectors.forEach((collectorInstance, name) => {
                callback(doc, name, collectorInstance);
            });
        });
    }

    // @Throws Exception
    getCollectorByName(doc, name) {
        const collectors = this._runningCollectorsPerDoc.get(doc);

        if (collectors && collectors.has(name)) {
            return collectors.get(name);
        }

        throw new Error(`Doc: ${doc.location}, didnt have collector named: "${name}"`);
    }

    removeCollector(collectorName) {
        this._runningCollectorsPerDoc.forEach((collectors, doc) => {
            if (collectors.has(collectorName)) {
                collectors.delete(collectorName);
                if (collectors.size === 0) {
                    this._runningCollectorsPerDoc.delete(doc);
                }
            }
        });
    }

    collectorExistsInAnyDoc(collectorName) {
        let exists = false;
        this._runningCollectorsPerDoc.forEach((collectors, doc) => {
            if (collectors.has(collectorName)) {
                //Implement upon found
                exists = true;
            }
        });
        return exists;
    }

    removeCollectorForDoc(doc, collectorName) {
        const collectors = this._runningCollectorsPerDoc.get(doc);
        if (!collectors) return;
        if (collectors.has(collectorName)) {
            collectors.delete(collectorName);
            if (collectors.size === 0) {
                this._runningCollectorsPerDoc.delete(doc);
            } else {
                this._runningCollectorsPerDoc.set(doc, collectors);
            }
        }
    }

    forEachCollectorNameInDoc(doc, callback) {
        const collectors = this._runningCollectorsPerDoc.get(doc);
        if (!collectors) return;
        collectors.forEach((collectorInstance, name) => {
            callback(name);
        });
    }

    removeDoc(doc) {
        this._runningCollectorsPerDoc.delete(doc);
    }

}