import DataCollector from '../../../DataCollector';
import Log from '../../../../technicalServices/log/Logger';
import { FontsDetectionContract, FontDataType } from '../../../../contract/staticContracts/FontsDetectionContract';
import FontVersionType from './types/FontVersionType';
import FontCollector from './FontCollector';
import FontMigrationStage from './types/FontMigrationStage';
import FontsCache from './FontsCache';
import FontsProvider from './FontsProvider';
import FontConfiguration from './FontConfiguration';

//Scanners
import OldBatchFontsScanner from './v1/OldBatchFontsScanner';
import OldOffloadFontsScanner from './v1/OldOffloadFontsScanner';
import FontsScanner from './v2/FontsScanner';


const featureSettings = {
    configKey: 'isFonts',
    isDefault: false,
    shouldRunPerContext: false,
    shouldRunPerSession: true,
    shouldRun: true,
    isFrameRelated: false,
    runInUns: true,
    runInSlave: false,
    runInLean: false,
    isRunning: false,
    instance: null,
};

/**
 * FontCollectionFeature
 *
 * Collects installed fonts using multiple detection strategies and manages the migration between different font collection versions.
 *
 * ## Detection Strategies:
 * - **Worker-based Detection**: Offloads font scanning to web workers for better performance and non-blocking execution.
 * 
 * ## Points To Know
 * - The amount of fonts on Safari is going to be higher than Chrome.
 * - The amount of fonts on old code is lower than the new code
 * - Old code had bug - OldOffLoadScanner provide 48 fonts - OldBatchScanner provides 45 fonts - it means different id for user
 * Update: we created remove duplicated fonts config so now it's the same amount of fonts
 * 
 * ## Resources
 * Background: https://biocatch.atlassian.net/wiki/spaces/~712020ea4044df5ae84c1db6c8150a825e5219/pages/edit-v2/5270142999?draftShareId=4672c69e-1654-4b31-800b-091a2b2c9df9
 * Migration Plan : https://biocatch.atlassian.net/wiki/pages/resumedraft.action?draftId=5298978821&draftShareId=e07d6273-5f3b-4dcd-b61f-38941df4f06e
 * Performance Measurements: https://biocatch.atlassian.net/wiki/spaces/~712020ea4044df5ae84c1db6c8150a825e5219/pages/5298815015/Fonts+Performance
 */
export default class FontCollectionFeature extends DataCollector {

    static getDefaultSettings() {
        return featureSettings;
    }

    /**
     * Constructor for FontCollectionFeature
     * @param {CDUtils} params.utils - Provides general utility methods.
     * @param {DOMUtils} params.domUtils - Handles DOM-related operations.
     * @param {DataQueue} params.dataQueue - Manages queuing and processing of font data.
     * @param {FontConfiguration} params.fontConfig - Provides configuration settings.
     * @param {FontCollector} [params.v1Collector=null] - Optional existing V1 collector.
     * @param {FontCollector} [params.v2Collector=null] - Optional existing V2 collector.
     * @param {FontTimingService} [params.fontTimingService=null] - Optional font timing service.
     * @throws {Error} If the font version is invalid or unsupported.
     */
    constructor({
        utils,
        domUtils,
        dataQueue,
        fontConfig,
        v1Collector = null,
        v2Collector = null,
        fontTimingService = null
    }) {
        super();

        this._logsTag = `[FONTS-COLLECTOR][MAIN]`;

        this._utils = utils;
        this._domUtils = domUtils;
        this._dataQueue = dataQueue;
        this._fontConfig = fontConfig;
        this._workerCommunicator = dataQueue._serverWorkerCommunicator;

        this._v1Collector = v1Collector;
        this._v2Collector = v2Collector;
        this._fontTimingService = fontTimingService;
    }

    async startFeature() {
        Log.info(`${this._logsTag} Starting font feature.`);

        // Ensures that the collectors are properly initialized with the correct configuration because config takes time to load.
        if (!this._v1Collector) {
            this._v1Collector = this._createFontCollector(FontVersionType.VERSION1);
        }
        if (!this._v2Collector) {
            this._v2Collector = this._createFontCollector(FontVersionType.VERSION2);
        }

        const fontCollectionConfig = this._fontConfig.getFontCollectionConfig();

        Log.info(`${this._logsTag} Font collection config: ${JSON.stringify(fontCollectionConfig)}`);

        const migrationMode = this._fontConfig.getMigrationMode(FontMigrationStage.DUAL_COLLECTION_V1_PRIORITY);

        if (migrationMode === FontMigrationStage.V1_ONLY) {
            const collectedV1Fonts = await this._v1Collector.collectFonts();
            this._enqueueFontData(FontDataType.V1_ONLY, [collectedV1Fonts, null]);
            return
        }

        // PHASE 1: Dual Collection with V1 Priority
        // ---------------------------------------------------------
        // In this phase, both V1 and V2 collectors are active. Data is collected
        // primarily using V1, and V2 data is collected for comparison purposes.

        if (migrationMode === FontMigrationStage.DUAL_COLLECTION_V1_PRIORITY) {
            const collectedV1Fonts = await this._v1Collector.collectFonts();
            this._enqueueFontData(FontDataType.V1_ONLY, [collectedV1Fonts, null]);

            const collectedV2Fonts = await this._v2Collector.collectFonts();
            this._enqueueFontData(FontDataType.V1_AND_V2, [collectedV1Fonts, collectedV2Fonts]);
        }

        // PHASE 2: Dual Collection with V2 Priority
        // ---------------------------------------------------------
        // In this phase, both collectors remain active, but data collection 
        // prioritizes V2. V1 data is collected for comparison.

        if (migrationMode === FontMigrationStage.DUAL_COLLECTION_V2_PRIORITY) {
            const collectedV2FontsPhase2 = await this._v2Collector.collectFonts();
            this._enqueueFontData(FontDataType.V2_ONLY, [null, collectedV2FontsPhase2]);

            const collectedV1FontsPhase2 = await this._v1Collector.collectFonts();
            this._enqueueFontData(FontDataType.V1_AND_V2, [collectedV1FontsPhase2, collectedV2FontsPhase2]);
        }

        // PHASE 3: Full Migration to V2
        // ---------------------------------------------------------
        // In this phase, only the V2 collector is active. All logic related
        // to V1 is removed, and only V2 data is collected and enqueued.
        if (migrationMode === FontMigrationStage.V2_ONLY) {
            const collectedV2FontsPhase3 = await this._v2Collector.collectFonts();
            this._enqueueFontData(FontDataType.V2_ONLY, [null, collectedV2FontsPhase3]);
        }


        // EXTRA: Parallel Collection
        // ---------------------------------------------------------

        if (migrationMode === FontMigrationStage.DUAL_COLLECTION_PARALLEL) {
            // Run both collectors in parallel
            const [collectedV1Fonts, collectedV2Fonts] = await Promise.all([
                this._v1Collector.collectFonts(),
                this._v2Collector.collectFonts()
            ]);

            // Then enqueue combined V1 and V2 fonts
            this._enqueueFontData(FontDataType.V1_AND_V2, [collectedV1Fonts, collectedV2Fonts]);
        }
    }


    stopFeature() {
        Log.info(`${this._logsTag} Stopping font feature.`);
        this._collectorHasStopped = true;
        this._v1Collector?.release()
        this._v2Collector?.release()
        this._v1Collector = null
        this._v2Collector = null
    }

    _enqueueFontData(dataType, fonts) {
        if (this._collectorHasStopped) return;
        if (!fonts) throw Error("IllegalState fonts undefined");

        const storedFontsMessage = new FontsDetectionContract(dataType, fonts).buildQueueMessage();
        this._dataQueue.addToQueue('static_fields', storedFontsMessage, false);
        Log.debug(`${this._logsTag} Fonts enqueued: Data type: ${dataType}`);
    }

    _createFontCollector(fontVersion) {
        const fontScanner = this._getFontsScanner({
            domUtils: this._domUtils,
            fontVersion: fontVersion,
            workerCommunicator: this._workerCommunicator
        });

        // Create other dependencies
        const fontsProvider = new FontsProvider();
        const fontsCache = new FontsCache(this._utils.StorageUtils);

        return new FontCollector({
            fontVersion: fontVersion,
            utils: this._utils,
            fontConfig: this._fontConfig,
            fontScanner: fontScanner,
            fontsProvider: fontsProvider,
            fontsCache: fontsCache,
            fontTimingService: this._fontTimingService
        });
    }

    _getFontsScanner({ fontVersion, domUtils, workerCommunicator }) {
         // Handle legacy V1 scanners
        // Do not delete OldBatchFontsScanner, it is used for the old version of the font collection and changing it will cause to diff font list
        const v1Config = this._fontConfig.getV1ScannerConfig();

        if (fontVersion === FontVersionType.VERSION1) {
            if (v1Config.offloadFontsCollectionEnabled) {
                return new OldOffloadFontsScanner(domUtils, v1Config.offloadFontsRemoveDuplicated);
            }
            return new OldBatchFontsScanner(domUtils);
        }

        // Choose scanner based on detector type
        // Try worker scanner first (if workerCommunicator is provided)
        Log.info(`${this._logsTag} Using worker scanner`);
        return new FontsScanner({
            workerCommunicator: workerCommunicator
        });
    }
}
