import DataCollectorV2 from "../DataCollectorV2";
import DependenciesSingleton from "../../injectors/DependenciesContainer";
import Log from "../../../technicalServices/log/Logger";


export default class HeapCollectorV2 extends DataCollectorV2 {

    static configKey = 'isHeapFeature';

    constructor(dataQ) {
        super();
        this._dataQ = dataQ;
        this._performance = window.performance;
    }

    updateConfig() {}

    getEvents() {
        return [];
    }

    handleEvent = async (event) => {
        Log.info('HeapCollector - Starting heap data collection');

        try {
            const heapData = this.collectHeapData();

            Log.info('HeapCollector - Heap Data collected: ' + JSON.stringify(heapData));

            this.sendData(heapData);
        } catch (error) {
            Log.error('HeapCollector - Error during data collection:', error);
        }
    }

    sendData(heapData) {
        this._dataQ.addToQueue('static_fields', ['heap', heapData], false);
    }

    collectHeapData() {
        const heapData = {
            timestamp: Date.now()
        };

        try {
            // Performance heap memory information
            if (this._performance && this._performance.memory) {
                const { usedJSHeapSize, totalJSHeapSize, jsHeapSizeLimit } = this._performance.memory;
                if (usedJSHeapSize !== undefined) {
                    heapData.usedJSHeapSize = usedJSHeapSize;
                }
                if (totalJSHeapSize !== undefined) {
                    heapData.totalJSHeapSize = totalJSHeapSize;
                }
                if (jsHeapSizeLimit !== undefined) {
                    heapData.jsHeapSizeLimit = jsHeapSizeLimit;
                }
            }

        } catch (error) {
            Log.info('HeapCollector - Error collecting heap data:', error);
        }

        return heapData;
    }
}