import {ConfigurationFields} from "../../core/configuration/ConfigurationFields";
import Log from "../log/Logger";

/**
 * CategoryService  Semantic Form Field Classifier (Shadow DOM aware + Cached)
 *
 * Purpose:
 * - Automatically classifies HTML form fields (e.g. <input>, <select>) into semantic categories
 *   such as "email", "password", "SSN", etc., based on their attribute values and associated labels.
 *
 * Features:
 * - Uses a keyword-to-category mapping (from elementCategories.json or provided).
 * - Extracts text from key attributes (name, id, placeholder, aria-label, etc.).
 * - Supports associated <label for="..."> matching.
 * - Traverses into Shadow DOM to find inner form elements and attribute values.
 * - Uses a WeakMap cache to avoid redundant computation (each element is scanned only once).
 * - Stops scanning early when a strong match is found (3 hits on a keyword).
 * - Uses configurable element selectors from elementSelectors.json or provided.
 *
 * Input:
 * - HTMLElement (form field or container element)
 *
 * Output:
 * - Category ID (string), or "0" if no category match is found.
 *
 * Typical Use:
 *   const cs = new CategoryService();
 *   const categoryId = cs.categoryField(myInputElement);
 */
export default class CategoryService {
    /**
     * @param {Record<string,number>|null} elementCategories  map keyword  ID
     * @param {{attributes:string[]}|null} elementAttributes  attributes to scan
     * @param {{selectors:string[]}|null} elementSelectors  element selectors to match
     */
    constructor(elementCategories = null, elementAttributes = null, elementSelectors = null) {
        const defaultCats = require('./elementCategories.json');
        const defaultAttrs = require('./elementAttributes.json');
        const defaultSelectors = require('./elementSelectors.json');

        const catSource = elementCategories && Object.keys(elementCategories).length
            ? elementCategories
            : defaultCats;

        this.CATEGORY_RULES = Object.create(null);
        for (const [key, value] of Object.entries(catSource)) {
            this.CATEGORY_RULES[key] = value;
        }

        const attrSource = elementAttributes?.attributes?.length
            ? elementAttributes
            : defaultAttrs;
        this.ATTRIBUTE_NAMES = attrSource.attributes;

        const selectorSource = elementSelectors?.selectors?.length
            ? elementSelectors
            : defaultSelectors;
        this.ELEMENT_SELECTORS = selectorSource.selectors.join(',');

        /** @private */
        this._cache = new WeakMap();
    }

    onConfigUpdate(configurationRepository) {
        const parseMaybeJson = (value) => {
            if (!value) return null;
            if (typeof value === 'string') {
                try {
                    return JSON.parse(value);
                } catch (e) {
                    return null;
                }
            }
            if (typeof value === 'object') return value;
            return null;
        };

        const catRaw = configurationRepository.get(ConfigurationFields.elementCategories);
        const catParsed = parseMaybeJson(catRaw);
        if (catParsed && Object.keys(catParsed).length) {
            const rules = Object.create(null);
            for (const [key, value] of Object.entries(catParsed)) {
                rules[key] = value;
            }
            this.CATEGORY_RULES = rules;
        }

        const attrRaw = configurationRepository.get(ConfigurationFields.elementAttributes);
        const attrParsed = parseMaybeJson(attrRaw);
        if (attrParsed?.attributes?.length) {
            this.ATTRIBUTE_NAMES = attrParsed.attributes;
        }

        const selectorRaw = configurationRepository.get(ConfigurationFields.elementSelectors);
        const selectorParsed = parseMaybeJson(selectorRaw);
        if (selectorParsed?.selectors?.length) {
            this.ELEMENT_SELECTORS = selectorParsed.selectors.join(',');
        }
    }

    /**
     * Categorise a form control.
     * @param {HTMLElement} el
     * @returns {string} Category ID string, `"0"` for no match, or `"-1"` on internal error.
     */
    categoryField(el) {
        // Handle null/undefined inputs gracefully
        try {
            if (!el || !(el instanceof HTMLElement)) {
                return '0';
            }

            const cached = this._cache.get(el);
            if (cached !== undefined) return cached;

            if (el.type === 'hidden') {
                this._cache.set(el, '0');
                return '0';
            }

            const ctrl = this._resolveControlNode(el);
            if (!ctrl) {
                this._cache.set(el, '0');
                return '0';
            }

            const strings = this._collectStrings(ctrl);
            if (!strings.length) {
                this._cache.set(el, '0');
                return '0';
            }

            const counts = Object.create(null);
            let bestId = '0';
            let bestHits = 0;

            outer: for (const str of strings) {
                for (const token of this._splitWords(str)) {
                    const id = this.CATEGORY_RULES[token];
                    if (id !== undefined) {
                        const hits = (counts[id] || 0) + 1;
                        counts[id] = hits;
                        if (hits > bestHits) {
                            bestId = id.toString();
                            bestHits = hits;
                            if (bestHits >= 3) break outer;
                        }
                    }
                }
            }

            this._cache.set(el, bestId);
            return bestId;
        } catch (e) {
            Log.error(`CategoryService.categoryField failed: ${e}`);
            return "-1";
        }
    }

    _resolveControlNode(el) {
        if (el.matches(this.ELEMENT_SELECTORS)) return el;
        if (el.shadowRoot) {
            const inner = el.shadowRoot.querySelector(this.ELEMENT_SELECTORS);
            if (inner) return inner;
        }
        return null;
    }

    _collectStrings(ctrl) {
        const out = [];
        const pushAttrValues = node => {
            for (const name of this.ATTRIBUTE_NAMES) {
                const v = node.getAttribute?.(name);
                if (v) out.push(v.toLowerCase());
            }
        };

        pushAttrValues(ctrl);
        if (ctrl.shadowRoot) ctrl.shadowRoot.querySelectorAll('*').forEach(pushAttrValues);

        const root = ctrl.getRootNode();
        if (ctrl.id) {
            // Try to find label in the root node first, then fallback to document
            let lbl = root.querySelector(`label[for="${ctrl.id}"]`);
            if (!lbl && root !== document) {
                lbl = document.querySelector(`label[for="${ctrl.id}"]`);
            }
            if (lbl?.textContent) {
                const labelText = lbl.textContent.toLowerCase().replace(/ /g, '');
                out.push(labelText);
            }
        }
        return out;
    }

    _splitWords(str) {
        return str
            .replace(/([a-z])([A-Z])/g, '$1 $2')
            .split(/[^a-z0-9]+/i)
            .filter(Boolean)
            .map(s => s.toLowerCase());
    }
}
