import DataCollector from '../../DataCollector';
import Log from '../../../technicalServices/log/Logger';
import WebGLTimingService from './service/WebGLTimingService';
import BrowserTimingService from './service/BrowserTimingService';
import CPUTimingService from './service/CPUTimingService';
import FontTimingService from './service/FontTimingService';
import AnimationTimingService from './service/AnimationTimingService';
import TimingCache from './TimingCache';
import CDUtils from '../../../technicalServices/CDUtils';
import MemoryTimingService from './service/MemoryTimingService';
import StorageTimingService from './service/StorageTimingService';

const featureSettings = {
  configKey: 'isTimingFeature',
  isDefault: false,
  shouldRunPerContext: false,
  shouldRunPerSession: false,
  shouldRun: false,
  isFrameRelated: false,
  runInUns: false,
  runInSlave: false,
  runInLean: false,
  isRunning: false,
  instance: null,
};

class TimingCollector extends DataCollector {
  static getDefaultSettings() {
    return featureSettings;
  }

  constructor(dataQ, fontTimingService, options = {}) {
    super();
    this._dataQ = dataQ;
    this._fontTimingService = fontTimingService;
    
    // Inject services or create defaults
    this._webglService = options.webglService || new WebGLTimingService();
    this._browserService = options.browserService || new BrowserTimingService();
    this._CPUTimingService = options.cpuTimingService || new CPUTimingService();
    this._animationTimingService = options.animationTimingService || new AnimationTimingService();
    this._memoryBandwidthService = options.memoryTimingService || new MemoryTimingService();
    this._storageTimingService = options.storageTimingService || new StorageTimingService();
    
    // Inject storage utils or use default
    const storageUtils = options.storageUtils || CDUtils.StorageUtils;
    this._timingCache = options.timingCache || new TimingCache(storageUtils);
    
    // Inject logger or use default
    this._logger = options.logger || Log;
  }

  async startFeature() {
    this._logger.info('[TimingCollector] Starting timing data collection');

    // Check for cached timing data first
    const cachedTimingData = this._timingCache.getStoredTimingData();
    if (cachedTimingData) {
      this._logger.info('[TimingCollector] Using cached timing data');
      this.sendData(cachedTimingData);
      return;
    }

    // Set up callback for when new font timing data is available
    this._fontTimingService.setOnNewRecordAvailableCallback(() => {
      this.collectFontTiming();
    });

    // Collect and send each timing data type independently
    this.collectWebGLTiming();
    this.collectBrowserTiming();
    this.collectCPUBenchmark();
    this.collectAnimationFrameTiming();
    this.collectMemoryBandwidthTiming();
    this.collectStorageTiming();
    // Don't collect font timing immediately - wait for callback
    // this.collectFontTiming();
  }

  /**
   * Collects and sends WebGL timing data independently
   */
  async collectWebGLTiming() {
    try {
      const webglTiming = await this._webglService.collectWebGLTiming();
      this._logger.debug('[TimingCollector] WebGL Timing Data collected: ' + JSON.stringify(webglTiming));
      this.sendData({ graphicTiming: webglTiming });
      this._updateCache({ graphicTiming: webglTiming });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during WebGL timing collection:', error);
    }
  }

  /**
   * Collects and sends browser timing data independently
   */
  async collectBrowserTiming() {
    try {
      const browserTiming = this._browserService.collectBrowserTiming();
      this._logger.debug('[TimingCollector] Browser Timing Data collected: ' + JSON.stringify(browserTiming));
      this.sendData({ browserTiming: browserTiming });
      this._updateCache({ browserTiming: browserTiming });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during browser timing collection:', error);
    }
  }

  /**
   * Collects and sends CPU benchmark data independently
   */
  async collectCPUBenchmark() {
    try {
      const cpuBenchmark = await this._CPUTimingService.runBenchmarks();
      this._logger.debug('[TimingCollector] CPU Benchmark Data collected: ' + JSON.stringify(cpuBenchmark));
      this.sendData({ cpuTiming: cpuBenchmark });
      this._updateCache({ cpuTiming: cpuBenchmark });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during CPU benchmark collection:', error);
    }
  }

  /**
   * Collects and sends FPS data independently
   */
  async collectAnimationFrameTiming() {
    try {
      const fps = await this._animationTimingService.getFPS();
      const fpsData = { fps: fps };
      this._logger.debug('[TimingCollector] FPS Data collected: ' + JSON.stringify(fpsData));
      this.sendData({ animationFrameTiming: fpsData });
      this._updateCache({ animationFrameTiming: fpsData });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during FPS collection:', error);
    }
  }

  /**
   * Collects and sends memory bandwidth timing data independently
   */
  async collectMemoryBandwidthTiming() {
    try {
      const memoryTiming = await this._memoryBandwidthService.measureMemoryBandwidth();
      this._logger.debug('[TimingCollector] Memory Bandwidth Timing Data collected: ' + JSON.stringify(memoryTiming));
      this.sendData({ memoryBandwidthTiming: memoryTiming });
      this._updateCache({ memoryBandwidthTiming: memoryTiming });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during memory bandwidth timing collection:', error);
    }
  }

  /**
   * Collects and sends storage timing data independently
   */
  async collectStorageTiming() {
    try {
      const storageTiming = await this._storageTimingService.measureAll();
      this._logger.debug('[TimingCollector] Storage Timing Data collected: ' + JSON.stringify(storageTiming));
      this.sendData({ storageTiming: storageTiming });
      this._updateCache({ storageTiming: storageTiming });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during storage timing collection:', error);
    }
  }

  /**
   * Collects and sends font timing data independently
   */
  async collectFontTiming() {
    try {
      const fontTiming = this._fontTimingService.collectFontTiming();
      this._logger.debug('[TimingCollector] Font Timing Data collected: ' + JSON.stringify(fontTiming));
      this.sendData({ fontTiming: fontTiming });
      this._updateCache({ fontTiming: fontTiming });
    } catch (error) {
      this._logger.error('[TimingCollector] Error during font timing collection:', error);
    }
  }

  /**
   * Updates the cache with new timing data
   * @param {Object} newData - New timing data to add to cache
   */
  async _updateCache(newData) {
    try {
      const existingData = this._timingCache.getStoredTimingData() || {};
      const updatedData = { ...existingData, ...newData };
      await this._timingCache.saveTimingData(updatedData);
    } catch (error) {
      this._logger.error('[TimingCollector] Error updating cache:', error);
    }
  }

  /**
   * Stops the timing collector and cleans up resources
   */
  stopFeature() {
    this._logger.info('[TimingCollector] Stopped timing data collection');
  }

  sendData(timingData) {
    this._dataQ.addToQueue('static_fields', ['timing', timingData], false);
  }
}

export default TimingCollector; 