import Log from '../../../../technicalServices/log/Logger';

/**
 * Storage Timing Service for measuring storage operation timings
 * High device variation - SSD vs HDD, browser storage APIs
 */
class StorageTimingService {
  constructor({ performance, log, localStorage, sessionStorage, indexedDB, math } = {}) {
    this._performance = performance || window.performance;
    this._log = log || Log;
    this._localStorage = localStorage || window.localStorage;
    this._sessionStorage = sessionStorage || window.sessionStorage;
    this._indexedDB = indexedDB || window.indexedDB;
    this._Math = math || Math;
  }

  /**
   * Measures all storage timings
   * @returns {Promise<Object>} Storage timing data
   */
  async measureAll() {
    try {
      const localStorageTime = await this.measureLocalStorageTime();
      const sessionStorageTime = await this.measureSessionStorageTime();
      const indexedDBTime = await this.measureIndexedDBTime();
      const bulkLocalStorageTime = await this.measureBulkLocalStorageTime();
      const storageStressTest = await this.measureStorageStressTest();

      return {
        localStorageTime: Number(localStorageTime.toPrecision(16)),
        sessionStorageTime: Number(sessionStorageTime.toPrecision(16)),
        indexedDBTime: Number(indexedDBTime.toPrecision(16)),
        bulkLocalStorageTime: Number(bulkLocalStorageTime.toPrecision(16)),
        storageStressTest: Number(storageStressTest.toPrecision(16))
      };
    } catch (error) {
      this._log.debug('StorageTimingService - Error measuring storage:', error);
      return this.getFallbackResult();
    }
  }

  async measureLocalStorageTime() {
    try {
      const testKey = 'storage_timing_test';
      const testData = this.generateTestData(1024); // 1KB instead of 256B
      const iterations = 100; // More iterations for better measurement
      
      const startTime = this._performance.now();
      for (let i = 0; i < iterations; i++) {
        this._localStorage.setItem(testKey + i, testData);
        this._localStorage.getItem(testKey + i);
        this._localStorage.removeItem(testKey + i);
      }
      const endTime = this._performance.now();
      return endTime - startTime;
    } catch (error) {
      this._log.debug('StorageTimingService - Error in localStorage:', error);
      return 0;
    }
  }

  async measureSessionStorageTime() {
    try {
      const testKey = 'storage_timing_test';
      const testData = this.generateTestData(1024); // 1KB instead of 256B
      const iterations = 100; // More iterations for better measurement
      
      const startTime = this._performance.now();
      for (let i = 0; i < iterations; i++) {
        this._sessionStorage.setItem(testKey + i, testData);
        this._sessionStorage.getItem(testKey + i);
        this._sessionStorage.removeItem(testKey + i);
      }
      const endTime = this._performance.now();
      return endTime - startTime;
    } catch (error) {
      this._log.debug('StorageTimingService - Error in sessionStorage:', error);
      return 0;
    }
  }

  async measureIndexedDBTime() {
    try {
      return new Promise((resolve) => {
        const dbName = 'storage_timing_test_db';
        const storeName = 'testStore';
        const request = this._indexedDB.open(dbName, 1);
        
        request.onupgradeneeded = (event) => {
          const db = event.target.result;
          db.createObjectStore(storeName);
        };
        
        request.onsuccess = (event) => {
          const db = event.target.result;
          const tx = db.transaction(storeName, 'readwrite');
          const store = tx.objectStore(storeName);
          const testData = 'x'.repeat(1024); // 1KB instead of 256B
          const iterations = 50; // More iterations
          
          const startTime = this._performance.now();
          let completed = 0;
          
          for (let i = 0; i < iterations; i++) {
            const addReq = store.add(testData, 'key' + i);
            addReq.onsuccess = () => {
              const getReq = store.get('key' + i);
              getReq.onsuccess = () => {
                const delReq = store.delete('key' + i);
                delReq.onsuccess = () => {
                  completed++;
                  if (completed === iterations) {
                    const endTime = this._performance.now();
                    db.close();
                    this._indexedDB.deleteDatabase(dbName);
                    resolve(endTime - startTime);
                  }
                };
              };
            };
          }
        };
        
        request.onerror = () => {
          resolve(0);
        };
      });
    } catch (error) {
      this._log.debug('StorageTimingService - Error in IndexedDB:', error);
      return 0;
    }
  }

  async measureBulkLocalStorageTime() {
    try {
      const testKeyPrefix = 'bulk_storage_timing_test_';
      const testData = this.generateTestData(2048); // 2KB instead of 256B
      const count = 200; // More operations instead of 50
      
      const startTime = this._performance.now();
      
      // Write phase
      for (let i = 0; i < count; i++) {
        this._localStorage.setItem(testKeyPrefix + i, testData);
      }
      
      // Read phase
      for (let i = 0; i < count; i++) {
        this._localStorage.getItem(testKeyPrefix + i);
      }
      
      // Update phase (overwrite existing)
      for (let i = 0; i < count; i++) {
        this._localStorage.setItem(testKeyPrefix + i, testData + '_updated');
      }
      
      // Read updated phase
      for (let i = 0; i < count; i++) {
        this._localStorage.getItem(testKeyPrefix + i);
      }
      
      // Delete phase
      for (let i = 0; i < count; i++) {
        this._localStorage.removeItem(testKeyPrefix + i);
      }
      
      const endTime = this._performance.now();
      return endTime - startTime;
    } catch (error) {
      this._log.debug('StorageTimingService - Error in bulk localStorage:', error);
      return 0;
    }
  }

  async measureStorageStressTest() {
    try {
      const testKeyPrefix = 'stress_test_';
      const smallData = this.generateTestData(64); // 64B
      const mediumData = this.generateTestData(1024); // 1KB
      const largeData = this.generateTestData(8192); // 8KB
      
      const startTime = this._performance.now();
      
      // Small data operations (many small writes)
      for (let i = 0; i < 500; i++) {
        this._localStorage.setItem(testKeyPrefix + 'small_' + i, smallData);
      }
      
      // Medium data operations
      for (let i = 0; i < 100; i++) {
        this._localStorage.setItem(testKeyPrefix + 'medium_' + i, mediumData);
      }
      
      // Large data operations
      for (let i = 0; i < 20; i++) {
        this._localStorage.setItem(testKeyPrefix + 'large_' + i, largeData);
      }
      
      // Mixed read operations
      for (let i = 0; i < 200; i++) {
        this._localStorage.getItem(testKeyPrefix + 'small_' + (i % 500));
        this._localStorage.getItem(testKeyPrefix + 'medium_' + (i % 100));
        this._localStorage.getItem(testKeyPrefix + 'large_' + (i % 20));
      }
      
      // Cleanup
      for (let i = 0; i < 500; i++) {
        this._localStorage.removeItem(testKeyPrefix + 'small_' + i);
      }
      for (let i = 0; i < 100; i++) {
        this._localStorage.removeItem(testKeyPrefix + 'medium_' + i);
      }
      for (let i = 0; i < 20; i++) {
        this._localStorage.removeItem(testKeyPrefix + 'large_' + i);
      }
      
      const endTime = this._performance.now();
      return endTime - startTime;
    } catch (error) {
      this._log.debug('StorageTimingService - Error in storage stress test:', error);
      return 0;
    }
  }

  generateTestData(size) {
    const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    let result = '';
    for (let i = 0; i < size; i++) {
      result += chars.charAt(Math.floor(this._Math.random() * chars.length));
    }
    return result;
  }

  getFallbackResult() {
    return {
      localStorageTime: 0,
      sessionStorageTime: 0,
      indexedDBTime: 0,
      bulkLocalStorageTime: 0,
      storageStressTest: 0
    };
  }
}

export default StorageTimingService; 