
// TODO: remove observers?
import Log from "../../technicalServices/log/Logger";

export default class MutationsEmitterV2 {

    _documentObservers = new WeakMap();

    observe(window, onMutation) {
        const doc = window.document;
        if (this._documentObservers.has(doc)) return;
        const nextObserver = new window.MutationObserver((mutations) => {
            mutations.forEach((mutationRecord) => {
                mutationRecord.addedNodes.forEach((node) => {
                    onMutation({ type: 'added', node });
                });
                mutationRecord.removedNodes.forEach((node) => {
                    onMutation({ type: 'removed', node });
                });
            });
        });
        nextObserver.observe(doc, {childList: true, subtree: true});
        this._documentObservers.set(doc, { instance: nextObserver });
        Log.info('Observing document:', doc.URL);
    }

    observeElement(element, onMutation) {
        if (this._documentObservers.has(element)) return;
        const nextObserver = new MutationObserver((mutations) => {
            mutations.forEach((mutationRecord) => {
                mutationRecord.addedNodes.forEach((node) => {
                    onMutation({ type: 'added', node });
                });
                mutationRecord.removedNodes.forEach((node) => {
                    onMutation({ type: 'removed', node });
                });
            });
        });
        nextObserver.observe(element, {childList: true, subtree: true});
        this._documentObservers.set(element, { instance: nextObserver });
        Log.info('Observing document:', element);
    }

    unobserve(target) {
        const record = this._documentObservers.get(target);
        if (!record) return;
        try {
            record.instance.disconnect();
        } catch (e) {
            // ignore
        }
        this._documentObservers.delete(target);
        Log.info('Stopped observing:', target);
    }

}