import DataCollector from '../../DataCollector';
import Log from '../../../technicalServices/log/Logger';
import ArchitectureDetectorService from './ArchitectureDetectorService';
import CoreEstimatorService from './CoreEstimatorService';

const featureSettings = {
  configKey: 'isCPUFeature',
  isDefault: false,
  shouldRunPerContext: false,
  shouldRunPerSession: true,
  shouldRun: false,
  isFrameRelated: false,
  runInUns: false,
  runInSlave: false,
  runInLean: false,
  isRunning: false,
  instance: null,
};

class CPUDataCollector extends DataCollector {
  static getDefaultSettings() {
    return featureSettings;
  }

  constructor(dataQ) {
    super();
    this._dataQ = dataQ;
  }

  /**
   * Detects CPU architecture using the dedicated service
   * @returns {Promise<Object>} Object containing architecture test values
   */
  async detectArchitecture() {
    try {
      const detector = new ArchitectureDetectorService();
      return await detector.detectArchitecture();
    } catch (error) {
      Log.debug('CPUDataCollector - Error detecting architecture:', error);
      return {
        signByte: undefined,
        pointerSize: undefined,
        isLittleEndian: undefined,
        error: error.message
      };
    }
  }

  /**
   * Detects number of available cores using the dedicated service
   * @returns {Promise<number>} Number of available cores
   */
  async detectAvailableCores() {
    try {
      const estimator = new CoreEstimatorService();
      return await estimator.getCoreCount();
    } catch (error) {
      Log.debug('CPUDataCollector - Error detecting available cores:', error);
      return 1;
    }
  }

  async startFeature() {
    Log.info('CPUDataCollector - Starting CPU data collection');

    try {
      // Collect all CPU data using runtime detection only
      const [architectureData, availableCores] = await Promise.all([
        this.detectArchitecture(),
        this.detectAvailableCores()
      ]);

      const cpuData = {
        architecture: architectureData,
        availableCores
      };

      Log.debug('CPUDataCollector - CPU Data collected: ' + JSON.stringify(cpuData));

      this.sendData(cpuData);
    } catch (error) {
      Log.error('CPUDataCollector - Error during data collection:', error);
    }
  }

  sendData(cpuData) {
    this._dataQ.addToQueue('static_fields', ['cpu', cpuData], false);
  }
}

export default CPUDataCollector; 
