import ElementEventCollectorV2 from "../collectors/events/ElementEventCollectorV2";
import MouseEventCollectorV2 from "../collectors/events/MouseEventCollectorV2";
import KeyEventCollectorV2 from "../collectors/events/KeyEventCollectorV2";
import ContextPropsFeatureV2 from "../collectors/static/ContextPropsFeatureV2";
import HeapCollectorV2 from "../collectors/static/HeapCollectorV2";
import StorageEventCollectorV2 from "../collectors/events/StorageEventCollectorV2";
import DependenciesSingleton from "../injectors/DependenciesContainer";

export default class DataCollectorsFactory {

    create(collector) {
        if (collector === 'ElementEventCollectorV2') {
            return new ElementEventCollectorV2(
                DependenciesSingleton._msgBus,
                DependenciesSingleton._utils,
                DependenciesSingleton._elements,
                DependenciesSingleton._maskingService,
                DependenciesSingleton._devDebugDataQueue,
                DependenciesSingleton._configurationRepository,
            );
        } else if (collector === 'MouseEventCollectorV2') {
            return new MouseEventCollectorV2(
                DependenciesSingleton._utils,
                DependenciesSingleton._elements,
                DependenciesSingleton._devDebugDataQueue,
                DependenciesSingleton._maskingService
            );
        } else if (collector === 'KeyEventCollectorV2') {
            return new KeyEventCollectorV2(
                DependenciesSingleton._configurationRepository,
                DependenciesSingleton._utils,
                DependenciesSingleton._elements,
                DependenciesSingleton._devDebugDataQueue,
                DependenciesSingleton._msgBus,
                DependenciesSingleton._sameCharService,
                DependenciesSingleton._maskingService
            );
        } else if (collector === 'ContextPropsFeatureV2') {
            return new ContextPropsFeatureV2(
                DependenciesSingleton._devDebugDataQueue,
                DependenciesSingleton._perfMonitor,
                DependenciesSingleton._configurationRepository
            );
        } else if (collector === 'HeapCollectorV2') {
            return new HeapCollectorV2(
                DependenciesSingleton._devDebugDataQueue
            );
        } else if (collector === 'StorageEventCollectorV2') {
            return new StorageEventCollectorV2(
                DependenciesSingleton._utils,
                DependenciesSingleton._devDebugDataQueue,
                DependenciesSingleton._elements,
                DependenciesSingleton._msgBus,
            );
        }
    }

    configKey(collector) {
        switch (collector) {
            case 'ElementEventCollectorV2':
                return ElementEventCollectorV2.configKey;
            case 'MouseEventCollectorV2':
                return MouseEventCollectorV2.configKey;
            case 'KeyEventCollectorV2':
                return KeyEventCollectorV2.configKey;
            case 'ContextPropsFeatureV2':
                return ContextPropsFeatureV2.configKey;
            case 'HeapCollectorV2':
                return HeapCollectorV2.configKey;
            case 'StorageEventCollectorV2':
                return StorageEventCollectorV2.configKey;
        }
    }
}