import InlineStyleReader from './InlineStyleReader';
import {ConfigurationFields} from "../../core/configuration/ConfigurationFields";

const DEFAULT_ALLOWED_TAGS = ['div', 'style'];

export const DomInjectionIndicatorType = {
    unknown: -1,
    overlayDiv: 0,
    selectOpenStyle: 1,
};

export default class AgenticDomInjectionDetector {
    /**
     * @param {ConfigurationRepository} configurationRepository
     */
    constructor(configurationRepository) {
        this._configurationRepository = configurationRepository;
        this._allowedTags = this._getAllowedTags();
    }

    onConfigUpdate() {
        this._allowedTags = this._getAllowedTags();
    }

    /**
     * @param {Element} el
     * @returns {number} DomInjectionIndicatorType
     */
    detect(el) {
        if (!el || el.nodeType !== Node.ELEMENT_NODE) return DomInjectionIndicatorType.unknown;

        const tag = (el.tagName || '').toLowerCase();
        if (this._allowedTags.length > 0 && !this._allowedTags.includes(tag)) {
            return DomInjectionIndicatorType.unknown;
        }

        if (tag === 'div') {
            if (!el.hasAttribute('style')) return DomInjectionIndicatorType.unknown;
            const s = new InlineStyleReader(el);

            const position = s.get('position');
            const inset = s.get('inset');
            const pe = s.get('pointer-events');
            const opacity = s.get('opacity');
            const zIndex = s.get('z-index');

            const isFixed = position === 'fixed';
            const coversViewport = inset === '0px' || inset === '0' || inset === '0px 0px 0px 0px';
            const noPointerEvents = pe === 'none';
            const lowOpacity = (() => {
                const n = Number(opacity);
                return Number.isFinite(n) && n > 0 && n <= 0.01;
            })();
            const extremeNegativeZ = (() => {
                if (!zIndex) return true; // allow empty/auto
                const zi = Number(zIndex);
                return Number.isFinite(zi) && zi <= -1000000;
            })();

            if (isFixed && coversViewport && noPointerEvents && lowOpacity && extremeNegativeZ) {
                return DomInjectionIndicatorType.overlayDiv;
            }
        }

        if (tag === 'style') {
            const t = String(el.textContent || '').toLowerCase();
            if (t.includes('select:open') && t.includes('@keyframes') && t.includes('animation')) {
                return DomInjectionIndicatorType.selectOpenStyle;
            }
        }

        return DomInjectionIndicatorType.unknown;
    }

    _getAllowedTags() {
        let tags = this._configurationRepository?.get(ConfigurationFields.agenticDomInjectionBlinkTags);

        // Allow config to arrive as JSON string from server.
        if (typeof tags === 'string') {
            try {
                tags = JSON.parse(tags);
            } catch {
                tags = null;
            }
        }

        return Array.isArray(tags) ? tags : DEFAULT_ALLOWED_TAGS;
    }
}

