import DataCollector from '../../../DataCollector';
import Log from '../../../../technicalServices/log/Logger';
import BrowserExtensionsUrlScanner from './BrowserExtensionsUrlScanner';
import BrowserExtensionsUrlContract from '../../../../contract/staticContracts/BrowserExtensionsUrlContract';

const featureSettings = {
  configKey: 'isBrowserExtensionsUrlFeature',
  isDefault: false,
  shouldRunPerContext: false,
  shouldRunPerSession: true,
  shouldRun: false,
  isFrameRelated: false,
  runInUns: false,
  runInSlave: false,
  runInLean: false,
  isRunning: false,
  instance: null,
};

/**
 * BrowserExtensionsUrlCollector with URL-based detection
 */
export default class BrowserExtensionsUrlCollector extends DataCollector {
  static getDefaultSettings() {
    return featureSettings;
  }

  constructor(configurationRepository, utils, domUtils, devDebugDataQueue, urlExtensionsScanner = null) {
    super();

    this._configurationRepository = configurationRepository;
    this._domUtils = domUtils;
    this._utils = utils;
    this._devDebugDataQueue = devDebugDataQueue;
    this._urlExtensionsScanner = urlExtensionsScanner || new BrowserExtensionsUrlScanner(window);
  }

  /**
   * Start the extension detection feature
   */
  async startFeature() {
    try {
      const urlExtensions = await this._urlExtensionsScanner.scan();
      Log.debug(`[BrowserExtensionsUrlCollector] Browser extensions URL scan result: ${JSON.stringify(urlExtensions)}`);
      this._sendData(urlExtensions);
    } catch (error) {
      Log.error(`[BrowserExtensionsUrlCollector] Failed to detect browser extensions via URL: ${error.message}`);
      this._sendData([]);
    }
  }

  /**
   * Send the detected extensions data to the queue
   * @param {Array} extensionsList - List of detected extensions with id and timestamp
   */
  _sendData(extensionsList) {
    const browserExtensionsUrlContract = new BrowserExtensionsUrlContract(extensionsList);
    const contractData = browserExtensionsUrlContract.buildQueueMessage();
    this._devDebugDataQueue.addToQueue('static_fields', contractData, false);
  }
}

