// FontMathFeature.js (iframe-only)
import DataCollector from '../../../DataCollector';
import Log from "../../../../technicalServices/log/Logger";
import { measureInEphemeralIframe } from '../TypographyFrameUtils';  

const featureSettings = {
  configKey: 'isFontMathFeature',
  isDefault: false,
  shouldRunPerContext: false,
  shouldRunPerSession: true,
  shouldRun: false,
  isFrameRelated: false,
  runInUns: false,
  runInSlave: true,
  runInLean: true,
  isRunning: false,
  instance: null,
};

export default class FontMathFeature extends DataCollector {
  static getDefaultSettings() { return featureSettings; }

  constructor(dataQ, window = global.window, document = global.document) {
    super();
    this._dataQ = dataQ;
    this._window = window;
    this._document = document;
  }

  _buildMathML(doc) {
    const M = 'http://www.w3.org/1998/Math/MathML';
    const math = doc.createElementNS(M, 'math');
    const mrow = doc.createElementNS(M, 'mrow');
    const munderover = doc.createElementNS(M, 'munderover');

    //  logical structure identical to the old code (no <none>, no <mprescripts>)
    const mmPi = doc.createElementNS(M, 'mmultiscripts');
    const moPi = doc.createElementNS(M, 'mo'); 
    moPi.textContent = '';
    mmPi.appendChild(moPi);
    munderover.appendChild(mmPi);

    const data = [
      ["","","","",""],
      ["","","","",""],
      ["","","","",""],
      ["","","","",""],
      ["","","","",""],
      ["","","","",""],
    ];
    for (const [main, sub1, sub2, pre1, pre2] of data) {
      const mm = doc.createElementNS(M, 'mmultiscripts');
      const miMain = doc.createElementNS(M, 'mi'); miMain.textContent = main;
      const miSub1 = doc.createElementNS(M, 'mi'); miSub1.textContent = sub1;
      const miSub2 = doc.createElementNS(M, 'mi'); miSub2.textContent = sub2;
      const mpres  = doc.createElementNS(M, 'mprescripts');
      const miPre1 = doc.createElementNS(M, 'mi'); miPre1.textContent = pre1;
      const miPre2 = doc.createElementNS(M, 'mi'); miPre2.textContent = pre2;
      mm.append(miMain, miSub1, miSub2, mpres, miPre1, miPre2);
      munderover.appendChild(mm);
    }

    mrow.appendChild(munderover);
    math.appendChild(mrow);

    try { math.style.all = 'initial'; } catch (ex) {
      Log.warn('FontMathFeature: applying math.style.all failed', ex);
    }

    math.style.whiteSpace = 'nowrap';
    math.style.display = 'inline-block';
    math.style.letterSpacing = 'normal';
    math.style.lineHeight = 'normal';
    math.style.textTransform = 'none';
    math.style.fontVariant = 'normal';
    math.style.fontFeatureSettings = 'normal';
    math.style.fontKerning = 'auto';
    math.style.direction = 'ltr';
    math.style.writingMode = 'horizontal-tb';
    math.style.fontSize = '16px';
    return math;
  }

  async startFeature() {
    try {
      const info = await measureInEphemeralIframe({
        hostWindow: this._window,
        hostDocument: this._document,
        frameId: 'cd__frame__font_math',
        buildNode: ({ doc }) => this._buildMathML(doc),
        maxFrames: 12,
        minSamples: 3,
        stableHitsTarget: 2,
        absTolPx: 0,
        relTol: 0,
        quantStep: 0,
      });

      if (info) {
        this._dataQ.addToQueue('static_fields', ['font_math_info', info], false);
      }
    } catch (err) {
      Log.error('Failed to collect math font information', err);
    }
  }
}