import DataCollectorV2 from "../DataCollectorV2";
import DependenciesSingleton from "../../injectors/DependenciesContainer";
import {IsTrustedValue} from "../../../collectors/events/IsTrustedValue";
import {ElementEventType, EventStructure} from "../../../collectors/events/ElementEventCollector";
import CDUtils from "../../../technicalServices/CDUtils";
import {ConfigurationFields} from "../../../core/configuration/ConfigurationFields";

export default class ElementEventCollectorV2 extends DataCollectorV2 {

    static configKey = "isElementsEvent";

    constructor(msgBus, utils, elements, maskingService, dataQ, configurationRepository) {
        super();
        this._maxElemValLength = 200;
        this._inputEventsPerElement = new WeakMap();
        this._msgBus = msgBus;
        this._utils = utils;
        this._elements = elements;
        this._maskingService = maskingService;
        this._dataQ = dataQ;
        this._configurationRepository = configurationRepository;
        this._collectSelectElementBlurAndFocusEvents = this._configurationRepository.get(ConfigurationFields.collectSelectElementBlurAndFocusEvents);
    }

    getEvents() {
        // focus and blur do not propagate
        // we are using focusin and focusout instead.
        return [
            'input', 'focusin', 'focusout', // Input events
            'click', // Click events
            'change', // Select events
        ]
    }

    handleEvent = (event) => {
        // Filter out elements that don't match the condition
        const inputSelectors = "input:not([type='color']):not([type='hidden']):not([type='file'])" +
            ":not([type='image']):not([type='button']):not([type='radio']):not([type='checkbox']), textarea";
        if (event.target.matches(inputSelectors)) {
            if (event.type === 'input') {
                this._handleInputEvent(event);
            } else if (event.type === 'focusout' || event.type === 'focusin') {
                this._handleFocusBlurEvent(event);
            }
        }

        const clickSelectors = "input[type='button'], input[type='radio'], input[type='checkbox'], button";
        if (event.target.matches(clickSelectors)) {
            this._handleClickEvent(event);
        }

        const selectSelectors = 'select';
        if (event.target.matches(selectSelectors)) {
            if (event.type === 'change') {
                this._handleSelectElementOnChangeEvent(event);
            } else if (this._collectSelectElementBlurAndFocusEvents) {
                if (event.type === 'focusout' || event.type === 'focusin') {
                    this._handleFocusBlurEvent(event);
                }
            }
        }
    }

    updateConfig() {
        this._hashTruncationLength = this._configurationRepository.get('elementHashTruncationLength') !== undefined
            ? this._configurationRepository.get('elementHashTruncationLength') : this._hashTruncationLength;
        this._elements.updateFeatureConfig();
        // this._selectElementEvents.updateSettings(browserContext); TODO: this update locally
    }

    _handleInputEvent(e) {
        if (e.constructor.name === 'InputEvent') {
            this._inputEventsPerElement.set(e.target, {});
        }

        let newValue = e.target.value || '';
        if (newValue !== '') {
            // mask all chars, but preserve length
            newValue = this._maskingService.maskText(newValue, e.target.id);
        }
        this._sendToQueue(e, {
            length: newValue ? newValue.length : 0,
            elementValues: newValue,
            selected: -1,
        });
    }

    _handleFocusBlurEvent(e) {
        this._sendToQueue(e, {
            length: 0,
            selected: -1,
            elementValues: '',
        });
    }

    _handleClickEvent(e) {
        // in input of type button we wish to see the value in the button,
        // in input of type radio the value of the element clicked is the value
        // in input of type checkbox the value is the value property,
        // in button tag the value in the innerHTML
        // we send whether the value is checked un the selected property
        const {target} = e;
        let newValue = target.value || target.innerHTML || '';
        newValue = this._maskingService.maskText(newValue, e.target.id)
        this._sendToQueue(e, {
            length: newValue.length,
            elementValues: newValue,
            selected: target.checked === undefined ? -1 : (target.checked ? 1 : 0),
        });
    }

    _handleSelectElementOnChangeEvent(e) {
        let optionsVals = CDUtils.getDropDownListValues(e.target, this._maskingService).join(';');
        optionsVals = CDUtils.clearTextFromNumbers(optionsVals);
        const selected = e.target.selectedIndex;
        const {length} = e.target.value;

        this._sendToQueue(e, {
            length,
            selected,
            elementValues: optionsVals,
        });
    }

    _handleSyntheticInputEvents(e) {
        // jQuery autotab plugin special handling!
        // Backspace keys are not reported as custom input events in case of jQuery autotab plugin is applied on
        // the current target.
        // This allows any other subsequent custom key events (that are not backspace) to be reported as
        // input events despite having the current target hashed in _inputEventNameTags
        if (!!this._inputEventsPerElement.has(e.target)
            && !(typeof e.jQueryMask !== 'undefined'
                && (e.jQueryMask === 'autotab' || e.jQueryMask === 'prasleyValidator'))) {
            return;
        }

        let newValue = e.target.value || '';
        if (newValue !== '') {
            // mask all chars, but preserve length
            newValue = this._maskingService.maskText(newValue, e.target.id);
        }

        this._sendToQueue(e, {
            length: newValue ? newValue.length : 0,
            elementValues: newValue,
            selected: -1,
        });
    }

    _sendToQueue(e, eventData) {
        const isTrusted = IsTrustedValue[e.isTrusted];
        const time = CDUtils.dateNow();
        const relativeTimestamp = this.getTimestampFromEvent(e);
        let type = ElementEventType[e.type];

        if (this._utils.isUndefinedNull(type)) {
            type = -1;
        }

        const element = this._elements.getElementHashFromEvent(e);
        const eventSeq = this._utils.StorageUtils.getAndUpdateEventSequenceNumber();
        eventData = eventData || {};
        if (eventData.elementValues && eventData.elementValues.length > this._maxElemValLength) {
            eventData.elementValues = '';
        }

        const fullEventData = {
            ...eventData, ...{
                eventType: type,
                eventSequence: eventSeq,
                timestamp: time,
                elementHash: element,
                isTrusted: isTrusted,
                hashedValue: '',
                relativeTime: relativeTimestamp,
            }
        }

        this._dataQ.addToQueue('customer_metadata',
            [
                'elements_events_v2',
                this._utils.convertToArrayByMap(EventStructure, fullEventData),
            ],
            true
        );
    }

}